	module.exports = {"title":"Aufgabe  Diplomarbeit","body":"<h1 id=\"diplomarbeit\">Diplomarbeit</h1>\n<h3>Umfeld</h3>\n<p>Fr den Abschluss als diplomierte Gestalterin HF/diplomierter Gestalter HF, Fachrichtung Kommunikationsdesign, Vertiefung Interaction Design erarbeiten die Studierenden im 6. Semester eine Diplomarbeit. Die Abschlussnote der Ausbildung wird dabei durch Thesis (20%) und Diplom (80%) bestimmt.\nInhalte der Arbeit stehen unter einer <a href=\"https://creativecommons.org/licenses/by-sa/4.0/\" target=\"_blank\">CC-BY-SA 4.0 Lizenz</a> und werden publiziert. Es ist also darauf zu achten, dass keine Lizenzverstsse begangen werden.\nProgrammcode der Arbeit stehen under der <a href=\"https://opensource.org/licenses/MIT\" target=\"_blank\">MIT Lizenz</a> und werden publiziert.</p>\n<h3>Ziele</h3>\n<ul>\n<li>Gestalterisches Projekt zu einem selbstgewhlten Thema erarbeiten.</li>\n<li>Die Diplomarbeit kann auf den Erkenntnissen der  Thesisarbeit aufbauen.</li>\n<li>Entscheide ber Gestaltung und Arbeitsprozess mssen nachvollziehbar und professionell begrndet sein.</li>\n<li>Die Arbeit wird durch eine begleitende schriftliche Arbeit in einen Kontext eingeordnet (Umfang 30004000 Zeichen).</li>\n</ul>\n<h3>Prozess</h3>\n<p>Die Studierenden wurden whrend ihrer Ausbildung immer wieder mit gestalterischen Arbeiten beauftragt und verstehen, wie eine solche Arbeit zu bewltigen ist. Zudem gibt es im Semester mehrere Punkte, an denen die Studierenden Rckmeldung zu Ihrer persnlichen Arbeit erhalten knnen. Die Prfungsexperten sind in keiner dieser Prozessphasen involviert. Die Arbeit ist  selbstndig zu erarbeiten und wird von einem Mentorat begleitet.</p>\n<h3>Aufbau der Arbeit</h3>\n<p>Die Arbeit wird frei nach den Vorstellungen der Studierenden strukturiert. Je nach Thema und Art der Arbeit kann die Methode oder der Aufbau variieren. Die Informationsarchitektur und die ganze Arbeit soll nicht knstlerischer, sondern grafischer und dienender Natur sein. Somit gelten bliche Herangehensweisen und Regeln fr informierende Gestaltung. Die Arbeit muss vollstndig in einem Browser zugnglich sein.</p>\n<h3>Umfang</h3>\n<p>Die User-Experience muss mindestens 15 Minuten umschliessen, wenn die Diplomarbeit vollstndig konsumiert/erlebt wird (wiederkehrende Elemente werden nicht angerechnet). Es mssen mindestens vier Medientypen tragend bercksichtigt werden. Dazu zhlen: Text, Bild, Video, Creative-Code, Audio, 3D im Browser, Interaktions Mechaniken wie Games/Wizards/Chats/\nDa die Website mglichst lange zugnglich sein soll, sind alle Teile der Website selber zu hosten und Interaktion mit externen Diensten ist nicht gestattet oder bedarf einer Bewilligung durch die Lehrgangskoordination.</p>\n<h3>Mentorat</h3>\n<p>Die Aufgabe des Mentorats ist es, den Studierenden eine Rckmeldung whrend des Prozesses auf ihre gestalterische und inhaltliche Arbeit zu geben.<br>\nZum Mentorat sind alle Dozierenden berechtigt, die am Unterricht des Studiengangs beteiligt waren. Externe Mentorate knnen von den Studierenden zuhanden der Lehrgangskoordination vorgeschlagen werden und werden durch diese bewilligt.\nFr das Mentorat stehen pro Person 7 Stunden (nicht Lektionen) zur Verfgung. Es wird empfohlen dieses Zeitbudget in mehrere persnliche kurze Sitzungen und fr deren Vorbereitung zu nutzen. Die Mentorierenden sind angehalten sich an die Zeitvorgaben zu halten, da sonst ein unterschiedlicher Betreuungsgrad zwischen den Studierenden entsteht.\nDie Mentorierenden melden den Prozess anhand eines Fragebogens an die Lehrgangskoordination zurck.\nBei Anfragen an externe Dozierende kann eine Entschdigung von Fr. 840. in Aussicht gestellt werden.</p>\n<h3>Zeitlicher Ablauf</h3>\n<style>\n.nobreak {\n  white-space: nowrap;\n}\n\n.tab-spaced td,\n.tab-spaced th {\n  vertical-align: top;\n  padding-right: 1rem;\n}\n\n.tab-spaced td:last-child,\n.tab-spaced th:last-child {\n  padding-right: 0;\n}\n</style>\n<div class=\"tab-spaced\">\n<table>\n<thead>\n<tr>\n<th>Zeit</th>\n<th>Task</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>22.2.2020</td>\n<td>Diplom-Aufgabe abgeben</td>\n</tr>\n<tr>\n<td>4.4.2020</td>\n<td>Abgabe eines kurzen Exposs als <a href=\"https://github.com/logrinto/IAD2017.diplom/issues\" target=\"_blank\">GitHub-Issue</a> mit Thema/Projekt, Zeitplan sowie Mentoratswunsch. Server-Technologien mssen bis zu diesem Zeitpunkt beantragt werden.</td>\n</tr>\n<tr>\n<td>16.5.2020</td>\n<td>Review in der Klasse mit externem Dozent</td>\n</tr>\n<tr>\n<td>29.5.2020</td>\n<td>Technisches Review durch Alex Jenter</td>\n</tr>\n<tr>\n<td>05.6.2020</td>\n<td>Review in der Klasse mit Boris Prisset</td>\n</tr>\n<tr>\n<td>14.6.2020, 8:15 Uhr</td>\n<td>Abgabe der Diplom-Arbeit auf ein frei whlbares GitHub-Repository. URL zu Repository &amp; Screenshots aller wesentlichen Teile werden auf <a href=\"https://github.com/logrinto/IAD2017.diplom/issues\" target=\"_blank\">GitHub-Issue</a> abgegeben.</td>\n</tr>\n<tr>\n<td><span class=\"nobreak\">26.&amp;27.6.2020</span></td>\n<td>Verteidigung</td>\n</tr>\n<tr>\n<td>Juli &amp; August</td>\n<td>Benotung</td>\n</tr>\n</tbody>\n</table>\n</div>\n<h3>Abgabe</h3>\n<p>Die Abgabe erfolgt via ffentlich zugnglichem GitHub-Repository der studentischen Person. Der Source-Code liegt auf dem <code>master</code> branch. Statische Websites mssen auf dem Branch <code>gh-pages</code> gehostet werden. Sollte es weitere Server-Infrastruktur (node, php, Datenbank, ) brauchen, so muss diese bis zum 4.4.2020 beantragt werden. Externe Services sind nicht zulssig (Langzeitarchivierung).</p>\n<h3>Bewertungskriterien</h3>\n<h4>Technik (10%)</h4>\n<ul>\n<li>Die Abgegebenen Visualisierungen sind fr eine technische Realisation aussagekrftig</li>\n<li>Die Realisation mittels Browser-Technologie ist gegeben</li>\n<li>Code wird fehlerfrei ausgefhrt (Chrome &amp; Firefox)</li>\n<li>Responsive Ansicht ist gegeben</li>\n</ul>\n<h4>Inhalt (20%)</h4>\n<ul>\n<li>Wahl eines geeigneten Themas</li>\n<li>Informationsarchitektur bersichtlich und vollstndig</li>\n<li>Inhaltliche Strukturen wurden geschaffen</li>\n<li>Inhalte erfllen die Anforderung und sind vollstndig</li>\n</ul>\n<h4>Fhrung/Men (30%)</h4>\n<ul>\n<li>Interaktion ist schlssig und schnell zugnglich</li>\n<li>Interaktion wurde fr Touch-Screen optimiert</li>\n<li>Die aktuelle Position innerhalb der Informationsbeschaffung ist ersichtlich</li>\n<li>Die Lesefhrung ist berlegt und offensichtlich</li>\n<li>Gewohnte Interface-Konzepte</li>\n</ul>\n<h4>Grafisches Konzept (40%)</h4>\n<ul>\n<li>Das Grafische Konzept ist schlssig und durchgngig</li>\n<li>Grafische Sprache entspricht dem Inhalt</li>\n<li>Typografie berlegt eingesetzt</li>\n<li>Seitenarchitektur berlegt</li>\n<li>Zweckmssigkeit gegeben</li>\n<li>Themenvermittlung gelungen</li>\n</ul>\n<h3>Plagiate</h3>\n<p>Bei Plagiaten wird die ganze Arbeit mit der Note 1 bewertet. Als Definition von Plagiaten gilt die <a href=\"https://web.archive.org/web/20180718162232/http://www.uzh.ch/de/studies/teaching/plagiate.html\" target=\"_blank\">Definition der Universitt Zrich</a>.</p>\n<h3>Verteidigung  26.&amp;27.6.2020</h3>\n<p>Es wird eine mndliche Verteidigung (Fachgesprch) der Arbeit geben. Die anwesende Expertengruppe hat die Arbeit gesichtet und sind mit der Arbeit vertraut. Die Arbeit soll gezeigt werden und die berlegungen oder Entscheidungen die zur Arbeit fhrten darstellen. Die Verteidigung (Prsentation/Fachgesprch) zhlt zu 20 Prozent zur Note der Diplomarbeit.</p>\n<p>Der Ablauf der Verteidigung ist wie folgt:</p>\n<ul>\n<li>2 Minuten Einrichten</li>\n<li>15 Minuten Prsentation</li>\n<li>15 Minuten Fragen &amp; Antworten</li>\n<li>13 Minuten Besprechung der Experten</li>\n</ul>\n<h4>Verteidigung  26.6.2020</h4>\n<table>\n<thead>\n<tr>\n<th>Zeit</th>\n<th>Person</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>09:0009:45</td>\n<td>Nicole Watrinet</td>\n</tr>\n<tr>\n<td>09:4510:30</td>\n<td>Wolfgang Schoeck</td>\n</tr>\n<tr>\n<td>10:3011:15</td>\n<td>Natasha Ruf</td>\n</tr>\n<tr>\n<td>11:1512:00</td>\n<td>Tieu Khe Mayer</td>\n</tr>\n<tr>\n<td>13:1514:00</td>\n<td>Nils Mder</td>\n</tr>\n<tr>\n<td>14:0014:45</td>\n<td>Lars Mder</td>\n</tr>\n<tr>\n<td>14:4515:30</td>\n<td>Matthias Koch</td>\n</tr>\n<tr>\n<td>15:3016:15</td>\n<td>Severin Kilchhofer</td>\n</tr>\n</tbody>\n</table>\n<h4>Verteidigung  27.6.2020</h4>\n<table>\n<thead>\n<tr>\n<th>Zeit</th>\n<th>Person</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>09:0009:45</td>\n<td>Marc Hatt</td>\n</tr>\n<tr>\n<td>09:4510:30</td>\n<td>Stephanie Fuchs</td>\n</tr>\n<tr>\n<td>10:3011:15</td>\n<td>Marius Becker</td>\n</tr>\n<tr>\n<td>11:1512:00</td>\n<td>Pascale Anderegg</td>\n</tr>\n</tbody>\n</table>\n<h4>Nachtrag</h4>\n<p>80 Lektionen stehen fr die Betreuung der Diplomarbeit zur Verfgung.<br>\nDie Mentoratszeit ergibt sich wie folgt;</p>\n<p><code>(80 Lektionen  28 Lektionen Benotung) / 12 Studierende  1.6 Faktor zur Umrechung in Stunden = 7 Stunden</code></p>\n<p>Die Benotung aller Arbeiten wird durch drei Begleiter beurteilt. Diese haben ein Zeitbudget von 28 Lektionen.</p>\n"}

