	module.exports = {"title":"Aufgabe  Portfolio","body":"<h1 id=\"aufgabe-portfolio\">Aufgabe  Portfolio</h1>\n<div class='header'></div>\n<h2 id=\"aufgabe\">Aufgabe</h2>\n<p>Es wird fr eine reale oder fiktive Person ein portfolio aufgebaut. Dieses soll als Website funktionieren.</p>\n<h3>Termin</h3>\n<ul>\n<li>Am 29.Februar 2020  Es gibt vor der Klasse eine Prsentation der Wireframes und eine inhaltliche Struktur</li>\n<li>Am 12.Juni 2020 muss die Website online sein und eine Prsentation wird vor der Klasse gehalten. Abgabe per URL auf Slack.</li>\n</ul>\n<h2 id=\"hintergrund\">Hintergrund</h2>\n<p>Die Studierenden sollen durch die Aufgabe die Zeit erhalten, sich eine Grundstruktur fr ein Portfolio aufzubauen, welches Sie allenfalls spter persnlich benutzen knnten.</p>\n<h2 id=\"bewertung\">Bewertung</h2>\n<p>Da es sehr schwer fallen wird, eine objektive Bewertung pro Schlerin ab zu geben (unterschiedliche Vorstellungen was abzudecken ist), wird vor allem Umgang mit Struktur, Typografie und Gestaltung gezhlt. Es wird erwartet, dass ein grafisches System gezeigt wird, welches sich spter mit mehr Inhalt abfllen lsst. Es wird erwartet, dass mindestens 3 Arbeiten dokumentiert werden.</p>\n<ul>\n<li>Das Grafische Konzept ist schlssig und durchgngig</li>\n<li>Die publizierten Inhalte sind technisch einwandfrei</li>\n<li>Code wird fehlerfrei ausgefhrt (Chrome &amp; Firefox)</li>\n<li>Geeignete Erscheinung gewhlt</li>\n<li>Wurden gestalterische Prinzipien eingehalten</li>\n<li>Responsive Ansicht ist gegeben</li>\n<li>Typografie berlegt eingesetzt</li>\n<li>Seitenarchitektur berlegt</li>\n<li>Zweckmssigkeit gegeben</li>\n<li>Die Lesefhrung ist berlegt und offensichtlich Gewohnte Interface-Konzepte</li>\n<li>Text wurde mit Sorgfalt verfasst</li>\n<li>Inhaltliche Strukturen wurden geschaffen</li>\n</ul>\n"}

