	module.exports = {"title":"Untersuchung  Schriftmuster (Papier)","body":"<h1 id=\"untersuchung-schriftmuster-papier\">Untersuchung  Schriftmuster (Papier)</h1>\n<div class='header'></div>\n<h2 id=\"cast\">Cast</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Einleitung / Informationstext</li>\n<li>Alle 467 Glyphen in 24pt, Cast Regular</li>\n<li>Schriftschnitte Light, Regular, Medium, Bold, Heavy und Black</li>\n<li>Opentype-Features</li>\n<li>The quick brown fox jumps over the lazy dog.</li>\n<li>Infotext in verschiedenen Schriftschnitten und -grssen</li>\n<li>Formale Details</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Zum Einen fungiert das Papier-Schriftmuster als Plakat, welches die Schrift Cast anteasert. Zum Anderen werden die Spezifikationen und Anwendungen der Schrift kurz, bndig und bersichtlich auf den Punkt gebracht. Die Einleitung informiert ber kurz ber die Schrift, ihren Schpfer Domonique Kerber und deren Entstehung von 2007 bis 2011 an der ZHdK.</p>\n</div>\n<p><img src=\"../img/specimen/cast/cast-cover.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/cast/cast-folded.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/cast/cast-glyphes.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/cast/cast-opentype.jpg\" alt=\"specimen\"></p>\n<h2 id=\"dalton-maag\">Dalton Maag</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Schriftname und Auflistung der Schriftschnitte</li>\n<li>bersicht aller vorhandenen Glyphen</li>\n<li>Beschreibung der Schrift, besonderen Merkmale und ihr Anwendungsgebrauch</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Ist in ihrer Art und Weise kein klassisches Schriftenmuster, sondern dient mehr als ein Image- bzw. Designprodukt fr Dalton Maag. Mittels wenig Text werden besondere Merkmale der Schrift nur kurz angeschnitten, der Gedanke hinter der Herstellung sowie ihr Anwendungsbereich beschrieben. Der Hauptinhalt besteht in einer Wiederholung des Beschreibungstexts und unterscheidet sich untereinander in Grsse, Satzbreite und Schriftschnitt. Einige Seiten sind in der Gestaltung offen und besitzen mehr einen knstlerischen Wert und helfen mit ihrer visuellen Darstellung nicht wirklich der Schriftauswahl.</p>\n</div>\n<p><img src=\"../img/specimen/DaltonMaag/daltonmaag-01-cover.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/DaltonMaag/daltonmaag-02-font-interface.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/DaltonMaag/daltonmaag-03-font-glyphes.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/DaltonMaag/daltonmaag-04-font-design.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/DaltonMaag/daltonmaag-05-font-design.jpg\" alt=\"specimen\"></p>\n<h2 id=\"diplomstudiengang-zhdk\">Diplomstudiengang ZHdK</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Angaben zum Autor</li>\n<li>Gedanken und Ausgangslage</li>\n<li>Erstellungsprozess</li>\n<li>Symbolbibliothek</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Das kleine Bchlein beinhaltet 11 verschiedene Fonts, welche im Nachdiplomsstudiengang CAS Schriftgestaltung 12/13 an der ZHdK als Diplomarbeit entstanden sind. Zweck dieser kleinen Sammlung ist das Endresultat der Diplomarbeit als Sammlung zu prsentieren. Im Zentrum stehen die Gedankengnge die zum Endresultat gefhrt haben. Die Fonts befinden sich alle noch am Anfang ihrer Entwicklung, einzelne wurden weiterverfolgt und werden heute zum Kauf angeboten. Ein Beispiel hierfr ist die Cast von Dominique Kerber.</p>\n</div>\n<p><img src=\"../img/specimen/ZHdK/aussenseite.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/ZHdK/innenseite_1_Schriftanalyse.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/ZHdK/Innenseite_2_Symbole.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/ZHdK/Innenseite_3.jpg\" alt=\"specimen\"></p>\n<h2 id=\"eames\">Eames</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Schnitte Auflistung</li>\n<li>Kurzgeschichte, Intention</li>\n<li>Fotos mit Anwendungen</li>\n<li>Gesetzter Text in Anwendungen</li>\n<li>Kombinationen mit Sonderzeichen als Deko-Elemente und diverse farbige Variationen</li>\n<li>Zeichenmuster</li>\n</ul>\n<h3>Zweck</h3>\n<p>Der Verkauf der Schrift steht im Vordergrund.<br>\nDie Dokumentation ermglicht einen Einblick in viele mgliche Anwendungen diese Schriftfamilie.</p>\n<h3>Analyse</h3>\n<p>Das ist keinen Schriftenmuster in klassischem Sinn. Eher eine vielfltige und spielerische Darstellung der Familienschrift Eames. Interessant ist die Abwicklung der Entstehungsgeschichte ber mehrere Seiten. Als Weg mit Timeline abgebildet, zeigt der Weg wie kontinuierlich neue Elemente dazu gekommen sind.</p>\n</div>\n<p><img src=\"../img/specimen/Eames/IMG_6390.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6391.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6392.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6393.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6394.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6395.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Eames/IMG_6396.jpg\" alt=\"specimen\"></p>\n<h2 id=\"slanted-typographydesign\">Slanted Typography/Design</h2>\n<h3>Stencil.Type.  Winter 2009/10  Issue 09</h3>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Section 1: Stencil Type <br>Vorstellung verschiedener Schablonen-Schriften</li>\n<li>Section 2: Miscellaneous <br>Interviews, diverse Arbeiten, Projekte von Studierenden</li>\n<li>Section 3: Blog Life <br>Abgedruckte Auszge aus dem Web</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Das Magazin Slanted bietet Einblicke in aktuelle Arbeiten rund um die Themen Typografie und Design.<br>\nDie Ausgabe Nummer 09 befasst sich mit dem Thema Stencil-Fonts. Im Hauptteil werden verschiedene Schablonen-Schriften vorgestellt, ausserdem gibt es in dieser Ausgabe aber auch verschiedene andere Beitrge, so z. B. eine Reportage, Interviews oder in der Rubrik Fontnames Illustratet diverse illustrative Arbeiten.</p>\n</div>\n<p><img src=\"../img/specimen/Slanted/Slanted_1.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Slanted/Slanted_2.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Slanted/Slanted_3.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Slanted/Slanted_4.jpg\" alt=\"specimen\"></p>\n<h2 id=\"bibliothek-sg\">Bibliothek SG</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Inhalt (Kurzgeschichten)</li>\n<li>Schriftgeschichte</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Es handelt sich um ein Buch mit 5 Kurzgeschichten darin, einem Nachwort und einige Seiten ber die Schrift des Buches.<br>\nEs gibt 2 Anwender des Buches, die einen die gerne Lesen, den Autor mgen oder das Fachpublikum welches sich sehr fr Schriften interessieren, diese Kaufen das Buch wegen den letzten paar Seiten Zu dieser Schrift da der Autor ber ein enormes Wissen von Schrift verfgt.\nDas Buch wurde mit Bleisatz gedruckt und bediente sich an der Schrift French Old Style. Diese Schrift wurde im Jahr 1907/1908 entwickelt und hatte das Ziel die Effizienz der Satzherstellung zu steigern, besonders bei Zeitschriften und Zeitungen.</p>\n</div>\n<p><img src=\"../img/specimen/BibSG/Cover.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/BibSG/Inhalt.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/BibSG/Schriftmuster.jpg\" alt=\"specimen\"></p>\n<h2 id=\"offizin-haag-drugulin\">Offizin Haag-Drugulin</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Inhaltsverzeichnis ist nach Einsatz der Schriften und Schriftart unterteilt</li>\n<li>Als Einstieg einen Einblick in die Geschichte der Schrift</li>\n<li>Dann folgen die Schriftmuster. Jede Schriftart der Druckerei wird in verschiedenen Schriftgrssen, Schriftschnitten wie auch Gross- und Kleinschreibung aufgezeigt</li>\n<li>Nur bei den Werkschriften sieht man jeweils den ganzen Glyphensatz</li>\n</ul>\n<h3>Zweck</h3>\n<p>Dieses Buch diente der Druckerei als Vorzeigemuster fr Kunden. Damit konnten Kunden sehen was die Druckerei alles fr Schriften anbietet. So konnte man auch gleich zeigen was einem gefllt und was nicht.</p>\n<h3>Analyse</h3>\n<p>Es fehlen Hintergrundinfos der Schrift: Wer hat sie erstellt?, wann?, was sind Merkmale?, usw.\nUm einem Kunden eine Auswahl zeigen zu knnen eignet es sich gut aber sehr bersichtlich und strukturiert ist es nicht. Das Buch war natrlich sehr aufwendig zu erstellen und fr diese Zeit ein tolles Aushngeschild fr ein Geschft.</p>\n</div>\n<p><img src=\"../img/specimen/OHD/IMG_3231.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/OHD/IMG_3232.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/OHD/IMG_3233.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/OHD/IMG_3234.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/OHD/IMG_3235.JPG\" alt=\"specimen\"></p>\n<h2 id=\"helvetica-forever\">Helvetica forever</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Entstehung der Schrift</li>\n<li>Schriftentwicklung</li>\n<li>Durchbruch der Schrift</li>\n<li>Erfolg der Schrift</li>\n<li>spezielle Eigenschaften</li>\n<li>verbreitete Verwendung in Gestaltungsprojekten</li>\n<li>Konkurrenten</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Lars Mller verffentlichte dieses Buch aus kommerziellem Interesse in Kooperation mit Linotype aber auch aus privatem Interesse und fleissiger Helvetica-Nutzer.</p>\n<p>Im Hinblick zum 50-Jahr-Jubilum der Schrift Helvetica hat Lars Mller die Geschichte der Helvetica in einem Buch verfasst. Darin wird einerseits die Entstehung, die Entwicklung, der Durchbruch und Erfolg beschrieben und welche speziellen Eigenschaften die Helvetica ausmacht. Andererseits zeigt er die verbreitete Verwendung von Helvetica in verschiedenen bekannten Gestaltungsprojekten. Ebenso verweist er auf viele hnliche Schrift-Konkurrenten wie Beispielsweise die Akzidenz-Grotesk.</p>\n</div>\n<p><img src=\"../img/specimen/HelveticaForever/IMG_3230.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/HelveticaForever/IMG_3226.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/HelveticaForever/IMG_3228.JPG\" alt=\"specimen\">\n<img src=\"../img/specimen/HelveticaForever/IMG_3229.JPG\" alt=\"specimen\"></p>\n<h2 id=\"fedra\">Fedra</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>bersicht der Fedra Typefont nach den verschiednen Schnitten gegliedert.</li>\n<li>100 verschiedene Kombinationen der Schnitte.</li>\n<li>Tabellarische Auflistung der Schnitte mit Beispielen der Schrift selber.</li>\n<li>Die Entstehung der Schrift wird erlutert.</li>\n<li>Verschiedene Versionen werden miteinander verglichen.</li>\n<li>Mehrsprachigkeit wird anhand einer Block Auflistung nebeneinander aufgezeigt.</li>\n<li>Der Font wird in effektiven Beispielen gezeigt.</li>\n</ul>\n<h3>Zweck</h3>\n<p>Das Buch wird verwendet fr eine Darstellung der Font Fedra. Es zeigt alle mglichen Zusammenstellungen der Schnitte sowie Typefaces auf. Es ist an Designer sowie Typografen gerichtet.</p>\n<h3>Analyse</h3>\n<p>Die verschiedenen Arten die Schrift einzusetzen werden in diesem Buch klar aufgezeigt. Alle Schnitte knnen einfach miteinander verglichen werden. Zustzlich erhlt man wertvolle Informationen ber die Herkunft sowie Herstellung der Schrift. Am Schluss des Buches gibt es viele Beispiele, wie und wo die Schrift bereits gebraucht wurde.</p>\n</div>\n<p><img src=\"../img/specimen/Fedra/1_Fedra.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Fedra/2_Fedra.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Fedra/3_Fedra.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/Fedra/4_Fedra.jpg\" alt=\"specimen\"></p>\n<h2 id=\"year-book-of-type-2\">Year Book of Type 2</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Einstieg:<br>was, wie und wieso uberhaupt</li>\n<li>Schriftarten:<br>Doppelseite mit Schrift: links kann der Schriftdesigner den Raum nutzen um den Font nach seiner Wahl zu prsentieren, rechts findet man nhere detaillierte Informationen, Hintergrund zur Schrift, uber den Designer, Klassifizerung und Anwendung in verschiedenen Schnitten</li>\n<li>Index:<br>Auflistung aller gezeigten Schriften, Vergleich mglich</li>\n<li>Designer: <br>Verzeichnis aller im Buch genannten Font-Designer</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Das Buch ist 2015 erschienen und zeigt eine qualitativ hochwertige Auswahl an kurzlich erschienen Schriften. Bisher sind zwei Bnde erschienen, das dritte ist in Arbeit. Das Ziel des Werkes ist der Vertrieb der gelisteten Schriften und dient als Inspiration und ist ein Hilfsmittel bei der Suche einer Schrift. Der Herausgeber vom Year Book of Type ist slanted und vertrieben wird es von niggli. Auf slanted.de wird man mit Inspiration uber Fonts uberschuttet, der Blog bietet auch reichlich an Informationen sowie gibt es die Magazine die 4x jhrlich erscheinen. Trotzdem ist dieses Werk entstanden, um die Auswahl an Schriften in Printform darzustellen.</p>\n</div>\n<p><img src=\"../img/specimen/YearBook/cover.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/YearBook/designer.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/YearBook/index.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/YearBook/schriftarten.jpg\" alt=\"specimen\"></p>\n<h2 id=\"stern\">Stern</h2>\n<div class=\"margin\">\n<h3>Inhaltliche Teile</h3>\n<ul>\n<li>Geschichte der Entstehung, Hintergrnde</li>\n<li>Verwendungsempfehlung</li>\n<li>Bleisatzbeispiel</li>\n<li>Open-Type Funktionen</li>\n<li>Alternate Glyphs</li>\n<li>Anzahl Sprachen</li>\n<li>Zeichenbibliothek</li>\n<li>Impressum</li>\n</ul>\n<h3>Zweck und Analyse</h3>\n<p>Das kleine Booklet scheint sich an ein Fachpublikum zu richten, mit dem Zweck, die Idee und die Hintergrnde der Schrift aufzuzeigen.\nSchriftenliebhaber werden sicher die Fokusgruppe sein, an die sich das Booklet richtet. Der Zweck des Booklets wird wohl sein die Idee\neinem Fachpublikum zu verkaufen und sie auf den Font aufmerksam zu machen. Aber sicher auch als Hommage an den Prozess, die Leute die\ninvolviert waren und die innovative Idee, Bleisatz und Digitalfont gleichzeitig zu entwerfen.<br>\nDas kleine Booklet ist aus meiner Sicht eine Darstellungsart, wie man sie am ehesten ins Web bertragen knnte. Knapp, prgnant, mit\nklarer Struktur und vielen Bildern untermalt. Es wird mehr eine Geschichte erzhlt und eben diese Geschichte mit dem Font zusammen verkauft,\nals der Font alleine. So knnte ich mir am ehesten eine Prsentation im Web vorstellen, natrlich mit Interaktion angereichert. Diese Art\nvon Booklet halte ich fr Schriften-Liebhaber und Designer eine gute Lsung, da das oft auch die Personen sind, die mehr wissen wollen\nals nur, wie eine Schrift aussieht.</p>\n</div>\n<p><img src=\"../img/specimen/stern/20180223_151648.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/stern/20180223_151713.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/stern/20180223_151725.jpg\" alt=\"specimen\">\n<img src=\"../img/specimen/stern/20180223_151738.jpg\" alt=\"specimen\"></p>\n"}

