	module.exports = {"title":"Aufgabe  Fonts im Web","body":"<h1 id=\"aufgabe-fonts-im-web\">Aufgabe  Fonts im Web</h1>\n<div class='header'></div>\n<h2 id=\"ausgangslage\">Ausgangslage</h2>\n<p>Es gibt tausende von Webfonts (Schriftart) die man kostenpflichtig lizenzieren kann oder aber frei einsetzen darf. Durch die Vielfallt fllt es leicht, die bersicht zu verlieren und man bedient sich oft bei den Klassikern, von denen man die Qualitt und Vorzge bereits kennt. Um sich mit neuen Fonts vertraut zu machen und aber auch Kriterien bei der Wahl zu erlernen, soll jeder Student sich vertieft mit einem Font beschftigen.<br>\nDer Unterricht orientiert sich an der Font-Vorstellung, es werden jedoch auch Themen behandelt, die generell mit Webdesign zu tun haben.</p>\n<h2 id=\"aufgabe-ziel\">Aufgabe &amp; Ziel</h2>\n<p>Jeder Student whlt einen Font aus der sowohl Lizenzrechtlich frei zu verwenden ist, wie auch die Kriterien fr Mengentext (lineares Lesen) erfllt.</p>\n<p>Die Aufgabe umfasst folgende Punkte:</p>\n<ul>\n<li>Selektion des Fonts</li>\n<li>Recherche und Erarbeitung eines Textes ber Autor und Font</li>\n<li>Gestaltung eines Schriftmusters (Website)</li>\n<li>Abstimmung mit der Klasse um alle Schriftmuster als zusammenhnges Werk darzustellen</li>\n<li>Dokumentation des Vortschrittes und der Erkenntnisse</li>\n</ul>\n<h2 id=\"vorgehen-und-zeitplan\">Vorgehen und Zeitplan</h2>\n<p>Dieser Zeitplan umfasst lediglich die Tasks der hier gezeigte Aufgabe. Dazu kommen Inhalte und Aufgaben aus dem LAB-Unterricht.</p>\n<h3>KW08  23.02.2018</h3>\n<ul>\n<li>Was ist ein Schriftmuster  Funktion, Inhalt, Geschichte, Gegenwart</li>\n<li>Betrachtung, Analyse und Dokumentation von bestehenden Papier-Schriftmustern (jeweils zwei Personen untersuchen zwei Schriftmuster)</li>\n<li>Prsentation von Schriftmustern in Klasse</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>Welche Text-Schrift (keine Headline-Fonts) whle ich? Weshalb glaube ich, dass sich diese Schrift fr grosse Textmengen am Bildschirm eignet? Erste Analyse von Schrift: Gibt es Eigenheiten, die allenfalls besser sind als andere?</p>\n<p>Einige Schriften, die man bercksichtigen knnte.</p>\n<ul>\n<li>Source Sans &amp; Serif</li>\n<li>Ubuntu</li>\n<li>Droid Sans &amp; Serif</li>\n<li>PT Sans &amp; Serif</li>\n<li>Montserrat</li>\n<li>Lato</li>\n<li>Faustina</li>\n<li>Vollkorn</li>\n<li>Alegreya Sans &amp; Serif</li>\n<li>Merriweather Sans &amp; Serif</li>\n<li>Work Sans</li>\n<li>IBM Plex</li>\n<li>Barlow</li>\n<li>Open Sans</li>\n<li>Noto Sans &amp; Serif</li>\n<li>Fira Sans</li>\n<li>Amstelvar</li>\n<li><a href=\"https://rsms.me/inter/\" target=\"_blank\">Inter</a></li>\n</ul>\n<p>Abzugeben ist ein SVG, bei dem das Wort Hamburgefonts (klassisches Testwort fr Schriftmuster) gesetzt wird und spezielle Merkmale der Schrift farblich markiert sind. Die Legende dazu ist in einer Textdatei abzugeben. Siehe Beispieldatei.<br>\nZustzlich; Kenne ich digitale Schriftmuster? Bringen Sie drei URLs mit; Zwei Schriftenhersteller (nicht Linotype, Monotype, Fontshop, fonts.com, google-Fonts), eine Website bei der Schriften vorgestellt werden (Blog-Post oder hnliches). Kurze Vorstellung des Favoriten in der Klasse.</p>\n<h3>KW11  16.03.2018</h3>\n<ul>\n<li>Prsentation von Schrift in Klasse</li>\n<li>Dokumentation der Schriftwahl</li>\n<li>Worin unterscheidet sich meine Schrift von anderen Schriften?</li>\n<li>Was sind relevante Kriterien und Eigenschaften von Text-Schriften?</li>\n<li>Betrachtung, Analyse und Dokumentation von bestehenden digitalen Schriftmustern?</li>\n<li>Dokumentation der digitalen Schriftmuster</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>Abzugeben ist das korrigierte SVG von letzer Woche mit neu gelernten Kriterien oder Eigenheiten.\nZustzlich sollen die Proportionen der Schrift eingezeichnet werden (siehe Beispiel). Zustzlich soll ein Text mit 1000 Zeichen zur Schrift geschrieben werden, der die Geschichte/Hintergrund/Entstehung der Schrift beleuchtet. Quellen mssen korrekt und vollstndig angegeben werden. Ein kritischer Umgang mit der Zuverlssigkeit der Quellen ist gefordert. Dieser Text soll als Textdatei abgegeben werden.</p>\n<h3>KW12  23.03.2018</h3>\n<ul>\n<li>Prsentation von eigenschaften der Schrift in Verbindung mit der Geschichte. Warum sieht die Schrift so aus? Welchen Zweck sollte die Schrift erfllen. Warum gibt es die Schrift?</li>\n<li>Welche Mglichkeiten haben wir fr die Navigation innerhalb des Werkes (mobile first)?</li>\n<li>Dokumentation von verschiedenen Navigations-Konzepten.</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>Jeder Student macht ein Fact-Checking des bisher erarbeiteten Textes eines Mitstudenten. Stimmen alle Angaben? Wurde keine Wertung im Text eingearbeitet, sondern nur die Faktenlage dargestellt? Sind die eingezeichneten Merkmale wirklich relevant?</p>\n<h3>KW14  06.04.2018</h3>\n<ul>\n<li>Prsentation des Autors der eigenen Schrift</li>\n<li>Welche Inhalts-Elemente/Darstellungen sollen nun auf unserem Schriftmuster gezeigt werden</li>\n<li>Untersuchung ber grssen des Screens und Schriftgrssen</li>\n</ul>\n<h4>Heimarbeit:</h4>\n<p>Es soll ein Text mit 1000 Zeichen zum Designer/Zeichner der eigenen Schrift geschrieben werden. Hintergrund/Geschichte/Werdegang und weitere Schriften des selben Gestalters sollen erwhnt werden. Quellen mssen korrekt und vollstndig angegeben werden. Ein kritischer Umgang mit der Zuverlssigkeit der Quellen ist gefordert. Dieser Text soll als Textdatei abgegeben werden.<br>\nBeispiel-Websites mit der eigenen Schrift suchen. Gute und schlechte Punkte beim Einsatz des Fonts auf den Beispielen aufzeigen. Die Untersuchung beinhaltet zwei Listen (Positiv/Negativ) pro URL mit kurzen Stichworten. Abgabe als Textdatei.</p>\n<h3>KW16  20.04.2018</h3>\n<ul>\n<li>Erarbeitung von Schriftmuster-Beispielen (Mengentext)</li>\n<li>Media-Queries fr verschiedene Screen-Grssen</li>\n<li>Dokumentation von Beispiel-Websites</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>Es werden zwei Schriftmuster (HTML) abgegeben. Ein Muste, welches eine Anwendung als Grundtext zeigt bei dem der Text in Schwarz auf Weiss mglichst gut Lesbar gesetzt wird. Daneben soll ein Beispiel abgegeben werden, wie in Kombination mit einem (!) anderen Font eine mglichst ansprechende Typografische struktur erarbeitet werden kann.<br>\nJeder Student macht ein Fact-Checking des bisher erarbeiteten Textes eines Mitstudenten.</p>\n<h3>KW20  18.05.2018</h3>\n<ul>\n<li>Prsentation von Schriftmustern</li>\n<li>Besprechung und berarbeitung von Schriftmustern</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>berarbeitung der Schriftmuster und zusammenstellen der Texte und Bilder.</p>\n<h3>KW22  01.06.2018</h3>\n<ul>\n<li>Prsentation von Schriftmustern</li>\n<li>Besprechung und berarbeitung von Schriftmustern</li>\n</ul>\n<h4>Heimarbeit</h4>\n<p>berarbeitung der Schriftmuster und zusammenstellen der Texte und Bilder.</p>\n<h3>KW22  29.06.2018</h3>\n<ul>\n<li>Abgabe und Prsentation der Schriftmuster</li>\n</ul>\n<h2 id=\"bewertung\">Bewertung</h2>\n<h3>Technik (20%)</h3>\n<ul>\n<li>Die technischen Vorgaben sind eingehalten</li>\n<li>Die publizierten Inhalte sind technisch einwandfrei</li>\n<li>Responsive Ansicht ist gegeben</li>\n</ul>\n<h3>Inhalt (20%)</h3>\n<ul>\n<li>Wahl eines geeigneten Fonts (Text-Font)</li>\n<li>Informationen bersichtlich und vollstndig</li>\n<li>Inhaltliche Strukturen wurden geschaffen</li>\n<li>Alle Inhaltselemente wurden vollstndig erarbeitet und eingefgt</li>\n<li>Texte erfllen die Anforderung und sind vollstndig</li>\n<li>Text wurde mit Sorgfalt verfasst (Quellen/Vollstndigkeit)</li>\n<li>Wichtige Merkmale angezeichnet und erkannt</li>\n</ul>\n<h3>Fhrung/Men (10%)</h3>\n<p>Da die Studierenden dies gemeinsam erarbeiten, wir dies auch ber die Klasse hinweg bewertet.</p>\n<ul>\n<li>Menstruktur ist schlssig und schnell zugnglich</li>\n<li>Men wurde fr Touch-Devices optimiert</li>\n<li>Die aktuelle Position innerhalb der Website ist stets nachvollziehbar</li>\n<li>Die Lesefhrung ist berlegt und offensichtlich Gewohnte Interface-Konzepte</li>\n</ul>\n<h3>Grafisches Konzept (40%)</h3>\n<ul>\n<li>Das Grafische Konzept der Seite und des Schriftmuster ist schlssig und durchgngig</li>\n<li>Grafische Sprache entspricht dem Inhalt</li>\n<li>Typografie berlegt eingesetzt</li>\n<li>Seitenarchitektur berlegt</li>\n<li>Zweckmssigkeit gegeben</li>\n<li>Wissensvermittlung gelungen</li>\n</ul>\n"}

