	module.exports = {"title":"Fonts  Eigenschaften","body":"<h1 id=\"fonts-eigenschaften\">Fonts  Eigenschaften</h1>\n<div class=\"TOC\">\n<p><strong>Content</strong>\n<div class=\"table-of-contents\"><ul><li><a href=\"#vierliniensystem\">Vierliniensystem</a></li><li><a href=\"#anatomie-der-buchstaben\">Anatomie der Buchstaben</a></li><li><a href=\"#spezielle-buchstabenformen\">Spezielle Buchstabenformen</a></li><li><a href=\"#weiterfuhrende-informationen\">Weiterfhrende Informationen</a></li></ul></div></p>\n</div>\n<div class='header'></div>\n<h2 id=\"vierliniensystem\">Vierliniensystem</h2>\n<p>Die lateinische Schrift basiert auf einem so genannten Vierliniensystem. Alle Schriftzeichen des Alphabet orientieren sich an der Schriftlinie und haben maximal zwei weitere Linien, an denen Sie sich orientieren.</p>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/anatomy/SVG/font-lines.svg\" alt=\"Font anatomy\"></p>\n</div>\n<div class=\"grid fullsize\">\n<div class=\"col_6of12 inlinecode\">\n<ol>\n<li>Kegelunterkante  CSS: <code>text-bottom</code></li>\n<li>p-Linie (Decenderline, beardline)</li>\n<li>Schriftlinie, Grundlinie (Baseline)  CSS: <code>baseline</code></li>\n<li>x-Linie, Mittellinie (x-line)</li>\n<li>H-Linie, Versallinie (Cap line)</li>\n<li>k-Linie (Acenderline)</li>\n<li>Kegeloberkante  CSS: <code>text-top</code></li>\n</ol>\n</div>\n<div class=\"col_6of12_last ABC\">\n<ol>\n<li>x-Hhe, Mittellnge (x-Height)</li>\n<li>Unterlnge, Unterhhe (Decender depth)</li>\n<li>Versalhhe (Cap height)</li>\n<li>Oberlnge (Acender height)</li>\n<li>berhang (Overshoot)</li>\n<li>Vertikaler Raumbedarf, Kegelgrsse, Schriftgrsse (Font Size, em)</li>\n<li>Fleisch (buffer space, shoulder)</li>\n</ol>\n</div>\n</div>\n<br>\n<br>\n<style>\n.inlinebox {\n  width: 2em;\n  height: 0.1em;\n  background-color: red;\n  display: inline-block;\n  -webkit-print-color-adjust: exact;\n}\n.big-anatomy {\n  font-size: 2em;\n  line-height: 1.9;\n}\n.big-anatomy code {\n  font-size: inherit;\n}\n</style>\n<h3>CSS</h3>\n<p>Objekte in HTML knnen mittels CSS an der Schrift ausgerichtet werden.</p>\n<div class=\"big-anatomy\">\n  <code><span class=\"inlinebox\" style=\"\"></span>vertical-align: baseline; </code>\n  <code><span class=\"inlinebox\" style=\"vertical-align: text-top\"></span>vertical-align: text-top; </code>\n  <code><span class=\"inlinebox\" style=\"vertical-align: text-bottom\"></span>vertical-align: text-bottom; </code>\n</div>\n<br>\n<br>\n<br>\n<div class='header'></div>\n<h2 id=\"anatomie-der-buchstaben\">Anatomie der Buchstaben</h2>\n<div class=\"margin\">\n<ol>\n<li>Anstrich</li>\n<li>geschlossener Punze</li>\n<li>offener Punze</li>\n<li>Ohr, Fhnchen</li>\n<li>Abstrich</li>\n<li>Schulter</li>\n</ol>\n</div>\n<p><img src=\"./img/anatomy/SVG/font-anatomy-01.svg\" alt=\"Font anatomy\"></p>\n<div class=\"margin\">\n<ol>\n<li>Stamm, Grundstrich</li>\n<li>Haarstrich</li>\n<li>Serife</li>\n<li>Anstrich/Dachansatz</li>\n<li>Halbserife</li>\n<li>Schleife</li>\n<li>Achslage</li>\n</ol>\n</div>\n<p><img src=\"./img/anatomy/SVG/font-anatomy-02.svg\" alt=\"Font anatomy\"></p>\n<h2 id=\"spezielle-buchstabenformen\">Spezielle Buchstabenformen</h2>\n<div class=\"margin\">\n<ol>\n<li>Einstckiges a | Zweistckiges a</li>\n<li>Zweistckiges g | Dreistckiges g</li>\n<li>Krckstock l (Erik Spiekermann) | normales l</li>\n<li>Amerikanische 1 | Europische 1</li>\n</ol>\n</div>\n<p><img src=\"./img/anatomy/SVG/font-character-forms.svg\" alt=\"Font anatomy\"></p>\n<div class='header'></div>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch<br>\n+41 78 744 37 38</p>\n<h4>Dokumentgeschichte</h4>\n<p>mrz 2018: Erstellung</p>\n</div>\n<h2 id=\"weiterfuhrende-informationen\">Weiterfhrende Informationen</h2>\n<h3>Technik</h3>\n<ul>\n<li><a href=\"https://twitter.com/ellmerstefan/status/934190602421448705/photo/1\" target=\"_blank\">Panose Werte</a></li>\n</ul>\n"}

