	module.exports = {"title":"Fonts  Darstellung","body":"<h1 id=\"fonts-darstellung\">Fonts  Darstellung</h1>\n<div class=\"TOC\">\n<p><strong>Content</strong>\n<div class=\"table-of-contents\"><ul><li><a href=\"#darstellung-von-fonts\">Darstellung von Fonts</a></li><li><a href=\"#darstellung-ohne-graustufen\">Darstellung ohne Graustufen</a></li><li><a href=\"#hinting\">Hinting</a></li><li><a href=\"#truetype-vs-postscript\">TrueType vs. PostScript</a></li><li><a href=\"#darstellung-mit-graustufen\">Darstellung mit Graustufen</a></li><li><a href=\"#darstellung-mit-subpixel-rendering\">Darstellung mit Subpixel-Rendering</a></li><li><a href=\"#hochauflosende-darstellung-oled\">Hochauflsende Darstellung & OLED</a></li><li><a href=\"#farbige-fonts\">Farbige Fonts</a></li><li><a href=\"#weiterfuhrende-informationen\">Weiterfhrende Informationen</a></li></ul></div></p>\n</div>\n<div class='header'></div>\n<h2 id=\"darstellung-von-fonts\">Darstellung von Fonts</h2>\n<h3>Vektor zu Pixel</h3>\n<p>Heutige Fonts bestehen meist aus Vektor-Daten, die dann fr die Anzeige an einem Ausgabegert in eine Pixel-Matrix umgesetzt werden mssen. Diese Umrechnung von Vektoren zu Pixel kann auf verschiedene Arten geschehen. Die dafr zustndige Software wird Renderer genannt und ist je nach Hersteller unterschiedlich und liefert fr die selben Vektoren unterschiedliche Ergebnisse.</p>\n<h3>Grsse in Pixel</h3>\n<p>Die Darstellungsgrsse einer Schrift wird in PPM (pixels per em) gemessen. Also wie viele Pixel das Geviert (vertikaler Raumbedarf) einnimmt. Wird also ein Font auf eine Schriftgrsse von 20 Pixel eingestellt, so ist der vertikale Raumbedarf 20 Pixel hoch. Das effektive Schriftbild (Schwrzung) ist jedoch im allgemeinen kleiner und von Schrift zu Schrift verschieden. Siehe <a href=\"../font-anatomy/\">Fonts  Eigenschaften</a>.\nEine Fontdatei hat intern ein ein Koordinatensystem, welches sich in Einheiten pro Geviert berechnet bei PostScript-basierten Schriften ist dies meist 1000UPM (units per em) und bei TrueType-basierten Schriften meist 2048UPM.</p>\n<br>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_6of12\">\n<div class=\"grid w80p\">\n<p><img src=\"./img/normal/Arial_a_outline.png\" alt=\"Buchstabe a als Outline\"></p>\n</div>\n<h4>Font als Outline</h4>\n<p>Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_6of12_last\">\n<div class=\"grid w80p\">\n<p><img src=\"./img/normal/Arial_a_no_hint.png\" alt=\"Buchstabe a als Pixel ohne Hinting\"></p>\n</div>\n<h4>12Pixel Hhe (PPM)  S/W</h4>\n<p>Gerendert werden alle Pixel die zu mindestens\n50% innerhalb der Kontur liegen (ohne Hinting).</p>\n</div>\n</div>\n<br>\n<br>\n<h3>Auflsung</h3>\n<p>Je hher die Auflsung eines Buchstaben ist, um so klarer nhert sich die Pixel-Darstellung an die Vektorform an.\nEine hhere Auflsung pro Buchstabe kann erreicht werden, indem die Grsse des Buchstabens oder die Auflsung des Ausgabegerts erhht wird.</p>\n<br>\n<br>\n<div class=\"grid fullsize upscale\">\n<p><img src=\"./img/Microsoft_BW_Arial_a_waterfall@10x.png\" alt=\"Arial as in s/w gerendert\"></p>\n<h4>8 bis 48Pixel Hhe (PPM)</h4>\n<p>Microsoft Arial Unicode  MS-Renderer  S/W</p>\n</div>\n<div class='header'></div>\n<h2 id=\"darstellung-ohne-graustufen\">Darstellung ohne Graustufen</h2>\n<div class=\"margin\">\n<div class=\"grid w70p\">\n<p><img src=\"./img/Microsoft_BW_Arial_word_waterfall_8_18px@10x.png\" alt=\"Textworte in Arial als s/w in 818px mit Microsoft Renderer\"></p>\n</div>\n<h3>8 bis 18Pixel Hhe (PPM)</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  S/W</p>\n</div>\n<p>Bei kleinen Schriften oder aber bei gewissen Display-Technologien kann es sinnvoll sein, Schrift nur mit S/W darzustellen.</p>\n<br>\n<br>\n<br>\n<div class=\"grid upscale\">\n<p><img src=\"./img/Microsoft_BW_Arial_Hamburge_8px@10x.png\" alt=\"Textwort in Arial als s/w in 8px mit Microsoft Renderer\"></p>\n</div>\n<h3>8Pixel Hhe (PPM)</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  S/W</p>\n<br>\n<br>\n<br>\n<div class=\"grid upscale\">\n<p><img src=\"./img/Microsoft_BW_Arial_Hamburge_24px@10x.png\" alt=\"Textwort in Arial als s/w in 24px mit Microsoft Renderer\"></p>\n<h3>24Pixel Hhe (PPM)</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  S/W</p>\n</div>\n<h3>Unterschiede im Rendering</h3>\n<p>Bei der S/W-Darstellung von Schrift sind die Unterschiede zwischen verschiedenen Renderern viel weniger stark erkennbar als bei einer Darstellung mit Graustufen.</p>\n<br>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<div class=\"w70p\">\n<p><img src=\"./img/FreeType_BW_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit FreeType Renderer  2014\"></p>\n</div>\n<h4>FreeType-Renderer  S/W</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_4of12\">\n<div class=\"w70p\">\n<p><img src=\"./img/Microsoft_BW_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit Microsoft Renderer  2014\"></p>\n</div>\n<h4>MS-Renderer  S/W</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_4of12_last\">\n<div class=\"w70p\">\n<p><img src=\"./img/Apple_BW_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit Apple Renderer  2014\"></p>\n</div>\n<h4>Apple-Renderer  S/W</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"hinting\">Hinting</h2>\n<p>ber spezielle Instruktionen wird die Kontur einer Schrift so verformt, dass die Darstellung besser auf ein Pixel-Grid passt. Danach wird diese neue Kontur vom Renderer gerastert. Die Verformung ist auch so optimiert, dass zum Beispiel die x-Hhe ber einen ganzen Font hinweg in allen Zeichen gleich hoch erscheint.</p>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<h3>Font als Outline</h3>\n<p>Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_4of12\">\n<div class=\"w80p\">\n<p><img src=\"./img/normal/Arial_a_outline.png\" alt=\"Buchstabe a als Outline\"></p>\n</div>\n</div>\n<div class=\"col_4of12_last\">\n<div class=\"w80p\">\n<p><img src=\"./img/normal/Arial_a_no_hint.png\" alt=\"Buchstabe a als Pixel ohne Hinting\"></p>\n</div>\n</div>\n</div>\n<br>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<h3>Hinting  12Pixel Hhe (PPM)</h3>\n<p>Konturen werden optimiert fr Rasterung.</p>\n</div>\n<div class=\"col_4of12\">\n<div class=\"w80p\">\n<p><img src=\"./img/normal/Arial_a_hint_outline.png\" alt=\"Buchstabe a als Outline mit Hinting\"></p>\n</div>\n</div>\n<div class=\"col_4of12_last\">\n<div class=\"w80p\">\n<p><img src=\"./img/normal/Arial_a_hint_render_microsoft.png\" alt=\"Buchstabe a als Pixel mit Hinting\"></p>\n</div>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"truetype-vs-postscript\">TrueType vs. PostScript</h2>\n<p>Da der Aufwand einen Font mit guten Hints (Anweisungen) zu versehen sehr gross ist, sind nicht alle Fonts gleich gut optimiert und somit auch in der Qualitt der Anzeige extrem verschieden. Hufig werden Schriften, die vorwiedgend fr den Druck gezeichnet wurden, lediglich ber eine automatische Hinting-Funktion optimiert. Dies fhrt nicht zu der selben Qualitt in der Anzeige, wie wenn die Instruktionen von Hand eingefgt wurden.\nPostScript basierte Schriften (OpenType PS &amp; PostScript Type 1) ermglichen weniger genaues Hintig wie TrueType basierte Schriften (OpenType TT &amp; TrueType).</p>\n<br>\n<br>\n<h3>TrueType mit von Hand eingefgten Hints</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  S/W</p>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/Microsoft_BW_Arial_a_waterfall@10x.png\" alt=\"TrueType a in Microsoft Arial 5  48\">\nDie Stammstrken des Buchstaben wachsen gleichmssig an.</p>\n</div>\n<br>\n<br>\n<br>\n<h3>Autohints in PostScript</h3>\n<p>AMB Newut Medium  MS-Renderer  S/W</p>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/Microsoft_BW_Newut_a_waterfall@10x.png\" alt=\"PostScript a in AMB Newut Medium 5  48\">\nStammstrken und Schriftlinie wirken nicht durchgehend.</p>\n</div>\n<div class='header'></div>\n<h2 id=\"darstellung-mit-graustufen\">Darstellung mit Graustufen</h2>\n<div class=\"margin\">\n<div class=\"grid w70p\">\n<p><img src=\"./img/Microsoft_gray_Arial_word_waterfall_8_18px@10x.png\" alt=\"Textworte in Arial als Graustufe in 818px mit Microsoft Renderer\"></p>\n</div>\n<h3>8 bis 18Pixel Hhe (PPM)</h3>\n<p>MS-Renderer  Graustufen<br>\nMicrosoft Arial Unicode</p>\n</div>\n<p>Oft wird im Web und bei eBooks eine Darstellung der Schrift mit Schriftglttung gewhlt. Die Schrift erscheint weicher aber auch besser lesbar. Charaktereigenschaften in Zeichen knnen mit Graustufen auch in einem Pixel-Raster mit wenig Auflsung relativ gut erahnt werden.</p>\n<br>\n<br>\n<br>\n<div class=\"grid upscale\">\n<p><img src=\"./img/Microsoft_gray_Arial_Hamburge_8px@10x.png\" alt=\"Textwort in Arial als Graustufe in 8px mit Microsoft Renderer\"></p>\n</div>\n<h3>8Pixel Hhe (PPM)</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  Graustufen</p>\n<br>\n<br>\n<br>\n<div class=\"grid upscale\">\n<p><img src=\"./img/Microsoft_gray_Arial_Hamburge_24px@10x.png\" alt=\"Textwort in Arial als Graustufe in 24px mit Microsoft Renderer\"></p>\n<h3>24Pixel Hhe (PPM)</h3>\n<p>Microsoft Arial Unicode  MS-Renderer  Graustufen</p>\n</div>\n<h3>Unterschiede im Rendering</h3>\n<p>Bei der Darstellung von Zeichen mit Graustufen wird je nach eingesetztem Renderer der Unterschied zwischen den einzelnen Technologien sehr deutlich sichtbar.</p>\n<br>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<div class=\"w70p\">\n<p><img src=\"./img/FreeType_gray_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit FreeType Renderer  2014\"></p>\n</div>\n<h4>FreeType-Renderer  Graustufen</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_4of12\">\n<div class=\"w70p\">\n<p><img src=\"./img/Microsoft_gray_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit Microsoft Renderer  2014\"></p>\n</div>\n<h4>MS-Renderer  Graustufen</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n<div class=\"col_4of12_last\">\n<div class=\"w70p\">\n<p><img src=\"./img/Apple_gray_Arial_a_13px@100x.png\" alt=\"a in Arial als s/w in 13px mit Apple Renderer  2014\"></p>\n</div>\n<h4>Apple-Renderer  Graustufen</h4>\n<p>13Pixel Hhe (PPM)  Microsoft Arial Unicode</p>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"darstellung-mit-subpixel-rendering\">Darstellung mit Subpixel-Rendering</h2>\n<div class=\"margin\">\n</div>\n<p>Bei der Darstellung mit Subpixeln wird die Schrftglttung nicht nur ber verschiedene Graustufen erzeugt, sondern auch noch der Vorteil ausgenutzt, dass ein Bildschirm in horizontaler Richtung eine dreifach grssere Auflsung besitzt, da die RGB-Darstellung ber drei Farb-Zustnde in horizontaler Richtung geschieht. Dadurch wird die Darstellungsqualitt wesentlich verbessert.</p>\n<h3>Windows</h3>\n<p>Unter Windows wurde im Jahr 2001 mit Windows XP (ClearType) das erste mal Subpixel-Rendering eingefhrt. In Window 7 (2009) wurde mit der neuen Technologie <a href=\"https://www.webtype.com/info/articles/web-font-quality/\" target=\"_blank\">DirectWrite</a> eine Verbesserung in der Darstellung mit Subpixel-Rendering erzielt.</p>\n<h3>MacOS</h3>\n<p>Unter MacOS wurde Subpixel-Rendering mit <a href=\"https://daringfireball.net/2003/11/panther_text_rendering\" target=\"_blank\">MacOS X 10.3 (Panther)</a> im Jahr 2003 eingefhrt. MacOS 10.14 (Mojave) entfernt Subpixel-Rendering auf hochauflsenden Screens im 2018.</p>\n<div class=\"grid fullsize space3 upscale\">\n<div class=\"col_6of12\">\n<p><img src=\"./img/normal/W-enlargement-subpixel-no-antialias.png\" alt=\"Ohne Subpixel-Rendering\">\nSchematische Darstellung ohne Glttung</p>\n<div class=\"small\">\n<p>Bildquelle: <a href=\"http://de.wikipedia.org/wiki/Subpixel-Rendering\" target=\"_blank\">Subpixel-Rendering  Wikipedia</a></p>\n</div>\n</div>\n<div class=\"col_6of12_last\">\n<p><img src=\"./img/normal/W-enlargement-subpixel-rendering.png\" alt=\"Mit Subpixel-Rendering\">\nSchematische Darstellung mit Subpixel-Rendering</p>\n</div>\n</div>\n<div class=\"margin space3\">\n<h3>Apple-Renderer  S/W</h3>\n<p>Microsoft Arial Unicode  12Pixel Hhe (PPM)</p>\n</div>\n<p><img src=\"./img/Apple_BW_Arial_Hamburgefonts_12px@10x.png\" alt=\"Testwort ohne Subpixel-Darstellung\"></p>\n<br>\n<div class=\"margin space3\">\n<h3>Apple-Renderer  Subpixel</h3>\n<p>Microsoft Arial Unicode  12Pixel Hhe (PPM)</p>\n</div>\n<p><img src=\"./img/Apple_subpixel_Arial_Hamburgefonts_12px@10x.png\" alt=\"Testwort mit Subpixel-Darstellung\"></p>\n<div class='header'></div>\n<h2 id=\"hochauflosende-darstellung-oled\">Hochauflsende Darstellung &amp; OLED</h2>\n<p>Auf hochauflsenden Screens und auch bei OLED-Technologie wird meist kein Subpixel-Rendering mehr eingesetzt.</p>\n<div class=\"grid fullsize\">\n<div class=\"col_6of12\">\n<p><img src=\"./img/iOS11_SanFranciscoProText17ptReg_IMG_5010@100x.png\" alt=\"\"></p>\n<h4>Rendering ohne Subpixel-Rendering</h4>\n<p>iOS 11 <a href=\"https://developer.apple.com/design/human-interface-guidelines/ios/visual-design/typography/\" target=\"_blank\">San Francisco Pro Text Regular 17pt</a> (Screenshot)</p>\n</div>\n<div class=\"col_6of12_last\">\n<p><img src=\"./img/normal/iPhoneX_iOS11_SanFranciscoProText17ptReg_IMG_5010.jpg\" alt=\"\"></p>\n<h4>Darstellung ohne Subpixel-Rendering</h4>\n<p>iOS 11 San Francisco Pro Text Regular 17pt auf iPhone X (Foto)</p>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"farbige-fonts\">Farbige Fonts</h2>\n<div class=\"margin compact\">\n<h4>Weiterfhrende Links</h4>\n<ul>\n<li><a href=\"https://www.colorfonts.wtf/\" target=\"_blank\">bersicht Color Fonts</a></li>\n<li><a href=\"https://shadycharacters.co.uk/series/emoji/\" target=\"_blank\">History of Emoji</a></li>\n</ul>\n</div>\n<p>Es gibt vier verschiedene Mglichkeiten, wie in einem OpenType-Font farbige Elemente definiert werden knnen. Abweichende Standards wurden jeweils separat von folgenden Parteien entwickelt;</p>\n<ul>\n<li>Microsoft (COLR/CPAL)</li>\n<li>Apple (SBIX)</li>\n<li>Google (CBDT/CBLC)</li>\n<li>Adobe/Mozilla (SVG)</li>\n</ul>\n<p>Da es somit technisch kompliziert ist fr einen Renderer alle vier Definitionen zu untersttzen, ist die Verbreitung von farbigen Fonts sehr gering. Betriebsysteme bieten blicherweise einen farbigen Emoji-Font, passend auf das jeweilige Betriebsystem, an. Jedoch gibt es darber hinau wenige Fonts, die diese Technologie nutzen, da es auch fr Schriftgestalter technisch mhsam ist alle Standards zu untersttzen.</p>\n<h3>Einfrben</h3>\n<br>\n<p><img src=\"./img/winemoji.png\" alt=\"Emoji-Font und die verschiedenen Layer\"></p>\n<p>Einige Technologien untersttzen auch das Einfrben einzelner Layer oder Teile.</p>\n<p>Bildquelle: <a href=\"https://typography.guru/journal/windows-color-fonts/\" target=\"_blank\">Typography.Guru  Color Emoji</a>\n<br>\n<br></p>\n<h3>Fallback</h3>\n<div class=\"grid fullsize space2\">\n<div class=\"col_6of12\">\n<p><img src=\"./img/colorfontsFallback.png\" alt=\"Farbiger Font gerendert auf einem Renderer, der dies nicht untersttzt\"></p>\n<p>Farbige Fonts knnen nur von wenigen Programmen angezeigt werden. Renderer die Farbige Fonts nicht untersttzen zeigen lediglich die S/W-Version an.</p>\n</div>\n<div class=\"col_6of12_last\">\n<p><img src=\"./img/colorfontsEnahnced.png\" alt=\"Farbiger Font\"></p>\n</div>\n<p>Ist der Renderer in der Lage die neuen Instruktionen im Font zu lesen, so kann die farbige Version angezeigt werden.</p>\n</div>\n<div class='header'></div>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch<br>\n+41 78 744 37 38</p>\n<h4>Dokumentgeschichte</h4>\n<p>Mai 2014: Erstellung<br>\nSeptember 2015: Erweiterung<br>\nMrz 2017: Erweiterung<br>\nJuni 2018: Erweiterung</p>\n</div>\n<h2 id=\"weiterfuhrende-informationen\">Weiterfhrende Informationen</h2>\n<h3>Subpixel-Rendering</h3>\n<ul>\n<li><a href=\"http://de.wikipedia.org/wiki/Subpixel-Rendering\" target=\"_blank\">Subpixel-Rendering  Wikipedia</a></li>\n<li><a href=\"https://ieeexplore.ieee.org/document/6494674/\" target=\"_blank\">Subpixel rendering: from font rendering to image subsampling</a></li>\n<li><a href=\"https://wwwimages2.adobe.com/content/dam/acom/en/products/type/pdfs/FontsonMacOSX.pdf\" target=\"_blank\">Apple  Advanced Typography with Mac OS X</a></li>\n</ul>\n<h3>Farbige Fonts</h3>\n<ul>\n<li><a href=\"https://pixelambacht.nl/2014/multicolor-fonts/\" target=\"_blank\">Pixelambacht  multicolor fonts</a></li>\n<li><a href=\"https://color.typekit.com/\" target=\"_blank\">Color fonts from Adobe Type</a></li>\n<li><a href=\"https://helpx.adobe.com/typekit/using/ot-svg-color-fonts.html\" target=\"_blank\">OpenType-SVG color fonts</a></li>\n<li><a href=\"http://manufacturaindependente.com/colorfont/\" target=\"_blank\">Colorfont  JavaScript Solution</a></li>\n<li><a href=\"http://typography.guru/journal/windows-color-fonts/\" target=\"_blank\">Typography.Guru  Color Emoji</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/OpenType#Color\" target=\"_blank\">OpenType - Wikipedia</a></li>\n</ul>\n<h3>Andere Renderings</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Checkerboard_rendering\" target=\"_blank\">Checkerboard rendering</a></li>\n</ul>\n"}

