	module.exports = {"title":"Fonts  Verwendung","body":"<h1 id=\"fonts-verwendung\">Fonts  Verwendung</h1>\n<div class=\"TOC\">\n<p><strong>Content</strong>\n<div class=\"table-of-contents\"><ul><li><a href=\"#fonts-auf-websites\">Fonts auf Websites</a></li><li><a href=\"#font-konzepte-furs-web\">Font-Konzepte frs Web</a></li><li><a href=\"#variable-fonts\">Variable Fonts</a></li><li><a href=\"#fonts-finden-testen\">Fonts finden & testen</a></li><li><a href=\"#fontlizenzen\">Fontlizenzen</a></li><li><a href=\"#freefonts-kommerzielle-fonts\">Freefonts  Kommerzielle Fonts</a></li><li><a href=\"#iconfonts\">Iconfonts</a></li><li><a href=\"#weiterfuhrende-informationen\">Weiterfhrende Informationen</a></li></ul></div></p>\n</div>\n<div class='header'></div>\n<h2 id=\"fonts-auf-websites\">Fonts auf Websites</h2>\n<h3>Geschichte</h3>\n<div class=\"margin compact\">\n<h4>Browser mit Webfont-Unterstzung</h4>\n<ul>\n<li>Chrome 4.0+</li>\n<li>Internet Explorer 4+</li>\n<li>Firefox 3.5+</li>\n<li>Safari 3.1+</li>\n<li>Opera 10+</li>\n</ul>\n<p>Quelle: <a href=\"https://en.wikipedia.org/wiki/Web_typography\" target=\"_blank\">Web typography - Wikipedia</a></p>\n</div>\n<p>Seit Computer eine Darstellung an einem Bildschirm aufweisen, wurde immer wieder nach technischen und gestalterischen Mglichkeiten gesucht, Schriften mglichst den Wnschen des Gestalters entsprechend dar zu stellen. Als das World Wide Web 1993 durch das CERN frei gegeben wird, war die Formatierung noch nicht durch einen Gestalter zu beeinflussen. Lediglich der Browser bestimmte das aussehen. Mit der Einfhrung von CSS im Jahre 1996 wurde dann die Mglichkeit geschaffen, Schriften einem Inhalt zu zuweisen. Allerdings musste der entsprechende Font bereits auf dem Zielgert installiert sein. Bereits 1997 ermglichte Microsoft mit damaliger Technologie in Ihrem Browser das Laden von Fonts ber das Internet. Somit wurde es mglich, eigene Schriften einzusetzen. Doch diese Technologie war nur auf Internet Explorer (Windows) verfgbar und auch von den Font-Anbietern sehr schlecht akzeptiert. Erst im Jahre 2009 wurde auf Grundlage von OpenType ein neues Format namens WOFF (Web Open Font Format) entwickelt. Dieses Format wurde sehr schnell von Browser-Herstellern und von Font-Anbietern akzeptiert, so dass heute Problemlos auf allen wichtigen Browsern der vom Gestalter gewnschte Font dynamisch in die Seite geladen werden kann.</p>\n<h3>Web Safe Fonts (Systemfonts)</h3>\n<p>Unter Systemfonts wird hufig eine Auswahl an Schriften verstanden, die zu einer sehr hohen Wahrscheinlichkeit auf dem Betriebssystem installiert sind, auf dem ein Browser eine Website darstellt. Somit ist der Browser in der Lage diesen Font zu verwenden und es muss kein zustzlicher Font bertragen werden. Die Auswahl dieser Fonts ist sehr beschrnkt und variiert stark von Betriebssystem zu Betriebssystem. Um dennoch die Gestaltung mehr oder weniger unter Kontrolle zu behalten, werden in einem CSS sogenannte Fallbacks angegeben. Das heisst, wenn eine Schrift nicht vorhanden ist, so kommt eine andere zum Einsatz.</p>\n<h4>Beispiel</h4>\n<p>CSS-Definition mit Font-Fallbacks:</p>\n<div class=\"code\">\n<pre>\np {\n  font-family: Arial, Helvetica, sans-serif;\n}\n</pre>\n</div>\n<p>Es wird versucht zuerst Arial einzusetzen. Wenn keine Arial vorhanden ist, soll Helvetica zum Einsatz kommen und sonst eine beliebige Serifenlose.</p>\n<h3>Verbreitung und Vorteile von Webfonts</h3>\n<p>Meist kommt bei Webfonts das WOFF (Web Open Font Format) zum Einsatz. Seit 2014 sind auch die besser komprimierten WOFF 2.0 Dateien im Gebrauch, die heute von allen modernen Browsern untersttzt werden. Auch ltere Browser knnen mit lteren Font-Formaten (EOT)  Schriften einbinden.\nVerfgt man ber eine Lizenz eines Webfonts, so kann diese Schrift dynamisch zur Seite hinzugeladen werden. Der Gestalter weiss somit, wie seine Seite aussehen wird und kann die Typografie vollstndig kontrollieren.</p>\n<h4>Beispiel</h4>\n<div class=\"margin compact\">\n<h4>Fallback</h4>\n<p>Damit der Webfont von mglichst vielen Browsern gelesen werden kann, werden diverse Fallbacks definiert.</p>\n</div>\n<p>CSS-Definition fr Webfonts:</p>\n<div class=\"code\">\n<pre>\n@font-face {\n  font-family: 'Open Sans';\n  src: url('opensans-reg-web.eot'); /* IE9 */\n  src: url('opensans-reg-web.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n  url('opensans-reg-web.woff2') format('woff2'), /* Modern Browsers */\n  url('opensans-reg-web.woff') format('woff'), /* OK Browsers */\n  url('opensans-reg-web.ttf') format('truetype'); /* Safari, Android, iOS */\n  font-weight: normal; <! ** >\n  font-style: normal;\n}\n\np {\n  font-family: \"Open Sans\", Arial, Helvetica, sans-serif;\n}\n</pre>\n</div>\n<h4>Beispiel google-Fonts</h4>\n<div class=\"code\">\n<pre>\n@import url('https://fonts.googleapis.com/css?family=Open+Sans');\n\np {\n  font-family: 'Open Sans', sans-serif;\n}\n</pre>\n</div>\n<h3>Unicode Range</h3>\n<p>Mchte man nur eine speziefische Auswahl an Buchstaben (Subset) eines Fonts von einer anderen Font-Datei beziehen, so kann dies mittels <code>unicode-range</code> gemacht werden. Dies ist vor allem ntzlich, wenn wichtige Zeichen in einem Font fehlen (beispielsweise geschtztes schmales Leerzeichen) und von einer anderen Datei bezogen werden mssen.</p>\n<h4>Beispiel</h4>\n<p>CSS-Definition mit <code>unicode-range</code> aus zwei font-faces:</p>\n<div class=\"code\">\n<pre>\n@font-face {\n  font-family: 'Open Sans';\n  src: url('opensans-reg-web.eot');\n  src: url('opensans-reg-web.eot?#iefix') format('embedded-opentype'),\n  url('opensans-reg-web.woff2') format('woff2'),\n  url('opensans-reg-web.woff') format('woff'),\n  url('opensans-reg-web.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  unicode-range: U+000-5FF; /* Latin glyphs */  <! ** >\n}\n\n@font-face {\n  font-family: 'Open Sans';\n  src: url('special-web.eot');\n  src: url('special-web.eot?#iefix') format('embedded-opentype'),\n  url('special-web.woff2') format('woff2'),\n  url('special-web.woff') format('woff'),\n  url('special-web.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  unicode-range: U+0061; /* overwrite for letter a */  <! ** >\n}\n\np {\n  font-family: \"Open Sans\", Arial, Helvetica, sans-serif;\n}\n</pre>\n</div>\n<h3>OpenType features</h3>\n<div class=\"margin compact\">\n<h4>Links</h4>\n<ul>\n<li><a href=\"https://helpx.adobe.com/typekit/using/open-type-syntax.html#ordn\" target=\"_blank\">bersicht ber die OpenType-Features von Adobe</a></li>\n<li><a href=\"https://css-tricks.com/almanac/properties/f/font-feature-settings/\" target=\"_blank\">Anwendung  css-tricks</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/font-feature-settings\" target=\"_blank\">MDN</a></li>\n<li><a href=\"https://blog.bramp.net/post/2018/01/21/google-font-features/\" target=\"_blank\">Google vs. Font Features</a></li>\n</ul>\n</div>\n<p>Auch im Web sind OpenType features wie Ligaturen oder verschiedenen Zahlensets mglich. Der Browser-Support ist hierfr gut, jedoch mssen natrlich die entsprechenden Features im Font vorhanden sein.</p>\n<h4>Beispiele</h4>\n<p>CSS-Definition mit aktivierten Brchen:</p>\n<div class=\"code\">\n<pre>\n.bsp1  {\n    font-feature-settings: \"frac\";\n}\n</pre>\n</div>\n<br>\n<p>CSS-Definition mit aktivierten tabelarischen (<code>lnum</code>) Medivalziffern (<code>onum</code>):</p>\n<div class=\"code\">\n<pre>\n.bsp2  {\n    font-feature-settings: \"lnum\", \"onum\";\n}\n</pre>\n</div>\n<br>\n<p>CSS-Definition mit ausgeschalteten Ligaturen:</p>\n<div class=\"code\">\n<pre>\n.bsp3  {\n    font-feature-settings: \"liga\" 0;\n}\n</pre>\n</div>\n<h4>Gngige Features</h4>\n<p>Um bei eienm Font OpenType-Features zu nutzen, muss die Dokumentation vom Font/Schriftenhersteller konsultiert werden um zu erfahren, welche Features verfgbar sind.</p>\n<p>Einige gngige Features sind die auch in CSS abrufbar sind;</p>\n<table>\n<thead>\n<tr>\n<th>Feature</th>\n<th>Englisch</th>\n<th>Deutsch</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>liga</td>\n<td>Standard Ligatures</td>\n<td>Standard Ligatures</td>\n</tr>\n<tr>\n<td>dlig</td>\n<td>Discretionary Ligatures</td>\n<td>Bedingte Ligaturen</td>\n</tr>\n<tr>\n<td>lnum</td>\n<td>Lining figures</td>\n<td>Versalziffern</td>\n</tr>\n<tr>\n<td>onum</td>\n<td>Oldstyle figures</td>\n<td>Medivalziffern (Minuskelziffern)</td>\n</tr>\n<tr>\n<td>pnum</td>\n<td>Proportional figures</td>\n<td>Proportionale Ziffern (nicht gleiche Breite)</td>\n</tr>\n<tr>\n<td>tnum</td>\n<td>Tabular figures</td>\n<td>Tabellenziffern (gleiche Breite)</td>\n</tr>\n<tr>\n<td>frac</td>\n<td>Fractions</td>\n<td>Bruchziffern</td>\n</tr>\n<tr>\n<td>smcp</td>\n<td>Small caps</td>\n<td>Kapitlchen</td>\n</tr>\n<tr>\n<td>ss01ss20</td>\n<td>Stylistic sets</td>\n<td>Formatstze</td>\n</tr>\n<tr>\n<td>sups</td>\n<td>Superscript/Superior</td>\n<td>Hochgestellt</td>\n</tr>\n<tr>\n<td>subs</td>\n<td>Subscript/Infirior</td>\n<td>Tiefgestellt</td>\n</tr>\n<tr>\n<td>numr</td>\n<td>Numerator</td>\n<td>Zhler</td>\n</tr>\n<tr>\n<td>dnom</td>\n<td>Denominator</td>\n<td>Nenner</td>\n</tr>\n</tbody>\n</table>\n<div class='header'></div>\n<h2 id=\"font-konzepte-furs-web\">Font-Konzepte frs Web</h2>\n<h3>Responsive Grsse</h3>\n<p>Fonts knnen auf die grsse des Gerts oder die grsse des Fensters reagieren. So knnen zum Beispiel Schriftgrssen abhngig von der Fenstergrsse (Viewport) definiert werden. Oder aber wenn das Layout von einer zweispaltigen Darstellung in eine einspaltige Darstellung wechselt (bei mobilen Gerten), kann eine ander Schriftgrsse gewhlt werden.</p>\n<br>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/ViewportSizedAll.png\" alt=\"Schriftgrsse wird gemss Viewport angepasst\"></p>\n<p>Mit CSS ist es mglich je nach Fensterbreite die Schrift in der Grsse anzupassen.</p>\n</div>\n<br>\n<h3>Font abhngig von Fenstergrsse</h3>\n<p>Bei diesem noch eher neuen Konzept ist die Idee, dass sich nicht nur die Grsse des Fonts dem Fenster anpasst, sondern dass auch der Font selber dem Fenster oder der Bildschirmgrsse angepasst wird. Diese Technologie ist heute schon mglich.</p>\n<br>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/FontToWidthAll.png\" alt=\"Anpassen von Fonts gemss Fensterbreite\"></p>\n<p>Fontwechsel  Auswechseln von Fonts abhngig von der Fenstergrsse.</p>\n</div>\n<div class='header'></div>\n<h2 id=\"variable-fonts\">Variable Fonts</h2>\n<h3>Vorgeschichte</h3>\n<div class=\"margin compact\">\n<h4>Links</h4>\n<ul>\n<li><a href=\"https://v-fonts.com/\" target=\"_blank\">Verzeichnis von Variable Fonts</a></li>\n<li><a href=\"https://medium.com/@clagnut/get-started-with-variable-fonts-c055fd73ecd7\" target=\"_blank\">bersicht Variable Fonts</a></li>\n<li><a href=\"https://clagnut.com/blog/2389/\" target=\"_blank\">Getting started with variable fonts</a></li>\n<li><a href=\"https://webkit.org/blog/7051/variable-fonts-on-the-web/\" target=\"_blank\">Variable Fonts on the Web  WebKit</a></li>\n<li><a href=\"https://blog.typekit.com/2016/09/14/variable-fonts-a-new-kind-of-font-for-flexible-design/\" target=\"_blank\">The Typekit Blog  Variable fonts</a></li>\n</ul>\n</div>\n<p>Im Jahr 2015 entstanden die ersten JavaScript-Experimente mit denen Fonts live im Web-Browser interpoliert (Zwischenschnitt zwischen zwei Extrem-Schriftschnitten) wurden und dann fr die Anzeige verwendet wurden. Somit konnte auf die Umstnde der Anzeige (Bildschirmgrsse, Gert, ) reagiert werden. Dieses Konzept war jedoch technisch nicht ausgereift genug fr produktive Systeme. Es war eher ein proof of concept.</p>\n<br>\n<div class=\"grid\">\n<p><img src=\"./img/InterpolationAll.png\" alt=\"Anpassen von Fonts gemss Fensterbreite\"></p>\n</div>\n<p>Anpassen des Fonts je nach Fenstergrsse.</p>\n<p>Bildquelle: <a href=\"http://alistapart.com/d/412/interpolation/\" target=\"_blank\">A List Apart  Live font interpolation</a></p>\n<h3>Standardisierung</h3>\n<p>Im Oktober 2016 hatte ein breites Konsortium von diversen Herstellern den OpenType-Standard mit der Version 1.8 angekndigt. Adobe, Google, Microsoft und Andere haben zugesagt diesen Standard bald in Ihre Software-Produkte zu integireren. Neben diversen eher technischen Erneuerungen ist vor allem die Mglichkeit fr Variable Fonts fr Gestalter heraus zu streichen. Mit dieser Funktion knnen Fonts mehrere so genannte Design-Achsen haben. Man kann also Fonts so einrichten, dass Sie zum Beispiel von ganz Light zu super Black stuffenlos (oder mit vom Designer gewhlten Stufen) einstellbar sind. Aber auch andere Design-Achsen lassen sich frei vom Schriftdesinger bestimmen. Diese Technologie erinnert an die Multiple master fonts (1991), jedoch wird sich der neue Standard wohl besser verbreiten als die damalige Technologie.</p>\n<h3>CSS und Web</h3>\n<p>Im Moment werden Variable Fonts gerade in die Software-Produkte eingebaut. Erste Test-Versionen sind fr Entwickler zuggnlich. Folgende CSS-Attribute sind vom Standard vorgesehen:</p>\n<ul>\n<li><code>font-weight</code>  Schriftstrke als Zahl von 1999</li>\n<li><code>font-stretch</code>  Schriftbreite in % (50% = Condesed, 200% Extended)</li>\n<li><code>font-style</code>  Schriftlage zwischen -90 und +90</li>\n<li><code>font-optical-sizing</code>  Optische Schriftgrsen ein- oder ausschalten</li>\n</ul>\n<br>\n<p>Daneben lassen sich aber auch weitere Werte-Achsen wie folgt definieren;</p>\n<div class=\"margin\">\n<p>Hier wird jeweils eine Werte-Achse mit vier Buchstaben (vom Designer bestimmt) mit einem Wert angewandt.</p>\n</div>\n<div class=\"code\">\n<pre>\np {\n    font-variation-settings: \"wdth\" 600, \"wght\" 200, \"opsz\" 48;\n}\n</pre>\n</div>\n<h2 id=\"fonts-finden-testen\">Fonts finden &amp; testen</h2>\n<div class=\"margin compact\">\n<h4>Mieten von Desktop-Fonts</h4>\n<p>Auf fontstand.com knnen Desktop-Fonts gemietet werden. Dies ermglicht Entwrfe dem Kunden vor zu legen, ohne den Font gekauft zu haben.</p>\n</div>\n<p>Fonts fr Web- und Screendesign knnen von fast allen grossen und kleinen Font-Foundrys erworben werden. Es gibt vereinzelt Schriften, die (noch) nicht fr den Web-Einsatz lizenziert werden knnen, dies ist jedoch eher die Ausnahme. Fr Anwendungen ausserhalb des Webs  aber auf Screens  gibt es eher wenige Anbieter. So bietet zum Beispiel Linotype auch fr eBooks, Apps und Server-Anwendungen Lizenzen. Diese Lizenztypen sind jedoch bei anderen Firmen oft nur mit Verhandlungen zu erwerben und es gibt keine standardisierten Prozesse.\nDas Vorgehen zur Recherche von neuen Schriften unterscheidet sich nicht wesentlich vom finden von Schriften fr den Print-Bereich. Sucht man eine Schrift, deren Name schon bekannt ist, so kann direkt zum Anbieter gesprungen werden. Ist man eher auf der Suche nach einem bestimmten Stil, kann eine Font-Plattform wie myfonts.com genutzt werden um ein entsprechendes Design zu finden.</p>\n<h3>Testen</h3>\n<p>Beim Suchen von Schriften ist es ratsam, die Schrift mglichst in einem Live-Preview auf die Tauglichkeit am Bildschirm zu prfen. Deshalb knnen Fonts bei immer mehr Anbietern entweder auf der Seite getestet werden, oder aber Sie kooperieren mit Test-Platformen wie zum Beispiel typecast.com. Bei solch einem Dienst kann bereits vor dem Kauf geprft werden, ob die Schrift bei gewnschter Anwendung, Grsse und Schriftmischung auf dem Screen funktioniert.</p>\n<br>\n<br>\n<div class=\"margin\">\n<h3>Vor dem Kauf testen</h3>\n<p>Auf typecast.com knnen Text-Layouts bereits vor dem Kauf einer Schrift definiert und editiert werden.\nDies erleichtert die Wahl eines geeigneten Fonts und zudem kann dem Kunden bereits ein Eindruck der fertigen Site vermittelt werden.</p>\n</div>\n<div class=\"imageline\">\n<p><img src=\"./img/typecast.png\" alt=\"Fonts mit typecast.com testen\"></p>\n</div>\n<div class='header'></div>\n<h2 id=\"fontlizenzen\">Fontlizenzen</h2>\n<div class=\"margin compact\">\n<h4>Mieten (pro Monat)</h4>\n<ul>\n<li><a href=\"https://www.fonts.com/unlimited#pricing\" target=\"_blank\">fonts.com</a></li>\n<li><a href=\"https://typekit.com/plans\" target=\"_blank\">Typekit</a></li>\n</ul>\n<h4>Fonts einmalig kaufen</h4>\n<ul>\n<li><a href=\"https://www.urwpp.de/\" target=\"_blank\"> URW++</a> (zeitlich beschrnkt)</li>\n<li><a href=\"https://www.fontshop.com/\" target=\"_blank\"> Fontshop</a></li>\n</ul>\n<h4>Views kaufen</h4>\n<ul>\n<li><a href=\"https://www.linotype.com/de/\" target=\"_blank\"> linotype.com</a></li>\n</ul>\n</div>\n<p>Es gibt grundstzlich drei verschiedene Arten von kommerziellen Font-Lizenzen:</p>\n<h3>Miet-Lizenzen</h3>\n<p>Beim mieten von Lizenzen wird meist ein View-Volumen pro Monat gekauft unabhngig jedoch von der Anzahl der Fonts. So kann zum Beispiel bei fonts.com ein Account erworben werden, der 50000 Pageviews pro Monat auf einer Domain erlaubt. Wird diese Zahl berschritten, muss auf ein hherer Preisplan gewechselt werden. Oft kann ein Account fr diverse Projekte gleichzeitig verwendet werden. Die Fonts sind beim Anbieter gehostet.</p>\n<h3>Einmalige Lizenzkosten</h3>\n<p>Bei immer wenigen Anbietern kann eine Lizenz einmalig erworben werden. Bei einer solchen Lizenz wird diee Schrift meist auf dem eigenen Server installiert und der Schriftenanbieter vertraut darauf, dass der Kufer die Anzahl Views pro Monat, fr die Lizenziert wurde, nicht berschritten wird.</p>\n<h3>Kaufen von Views</h3>\n<p>Es gibt Anbieter, die Views verkaufen. Das heisst, dass mit jedem Besuch einer Webseite mit dem Font das erworbene Konto um ein View reduziert wird. Somit muss die Schrift von Zeit zu Zeit wieder mit neuen Views aktiviert werden, so dass immer gengend Views vorhanden sind. Oder aber das Konto erneuert sich immer am Ende des Monats neu.</p>\n<h3>Zeichensatz</h3>\n<p>Wird die Schrift auf einer Seite eingesetzt, bei der viele Sprachen oder sonst spezielle Zeichen vorkommen, so ist es empfehlenswert zu Prfen, ob die Schrift auch den ntigen Zeichensatz abdeckt. Auch OpenType-Features werden von den Schriftherstellern normalerweise vor dem Kauf angegeben.</p>\n<div class='header'></div>\n<h2 id=\"freefonts-kommerzielle-fonts\">Freefonts  Kommerzielle Fonts</h2>\n<p>Es gibt von diversen Font Foundries, Firmen und Enthusiasten qualitativ sehr hochwertige und gute Schriften, die frei verfgbar sind. Dabei gibt es diverse Abstufungen. Es gibt ganze Schriftfamilien, die frei sind, dann gibt es Schriftfamilien, bei denen einzelne Schnitte frei sind, oder es gibt Fonts, bei denen ein gewisser Zeichenumfang frei ist. Damit nicht bei den einzelnen Stellen im Web nach den Fonts gesucht werden muss, empfiehlt es sich, Portale zu verwenden, die einem die verschiedenen Fonts sortiert prsentieren oder eine schnelle Suche ermglichen. Auch bieten solche Portale zum Teil die Mglichkeit, die Fonts gleich von deren Server zu laden (google Fonts) oder gewisse Konvertierungen fr den Webgebrauch (fontsquirrel) zu erledigen.\nDie bentige Fontlizenz fr eine Website sollte mit dem Kunden geklrt werden und bereits in der Offerte sollte geklrt sein, wer die Lizenz zu erwerben hat.</p>\n<h3>Kunde oder Agentur</h3>\n<p>Aus finanzieller berlegung kann es attraktiv sein, die Schriftlizenzen fr alle Kunden ber ein Konto der Agentur zu kaufen. So knnen die Kosten auf die verschiedenen Webprojekte umgelegt werden. Dies hat jedoch auch den Nachteil, dass die Agentur die Verantwortung gegenber dem Kunden hat, den Account zu bezahlen und pflegen.</p>\n<br>\n<br>\n<br>\n<div class=\"margin\">\n<h4>fonts.com</h4>\n<p>Im nebenstehenden Beispiel sieht man, dass fr $32 pro Monat eine Million Views fr beliebig viele Domains erworben werden kann.</p>\n</div>\n<p><img src=\"./img/fontcom_subscription.png\" alt=\"Abonnement fr fonts.com\"></p>\n<div class='header'></div>\n<h2 id=\"iconfonts\">Iconfonts</h2>\n<p>Icons werden auf Websites oft verwendet um Inhalte schneller und einfacher zugnglich zu machen. Aus technischer Sicht ist es relativ einfach  fr einen Programmierer  diese Icons in einen Font zu konvertieren. Zudem haben solche Icons den Vorteil, dass Sie auf vielen Betriebsystemen und Browsern funktionieren und vektorbasiert aufgebaut sind. Als Nachteil ist zu beklagen, dass fr die Accessibility (Menschen mit Behinderung einen Zugang ermglichen) es oftmals keine gute Idee ist, die Icons mit Schrift zu machen, da die Zeichen dann als Inhalt verstanden werden und zum Beispiel Screen-Reader damit Probleme haben knnen.</p>\n<br>\n<br>\n<div class=\"margin\">\n<h3>Iconfonts optimieren</h3>\n<p>Fontello.com bietet die Mglichkeit aus diversen freien Icon-Fonts sich die ntigen Icons zu suchen und dann einen eignenen Icon-Font daraus zu erstellen. Auch knnen eigene Icons in den Font eingebunden werden.</p>\n</div>\n<div class=\"imageline\">\n<p><img src=\"./img/fontello.png\" alt=\"Iconfont von fontello.com\"></p>\n</div>\n<div class='header'></div>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch</p>\n<h4>Dokumentgeschichte</h4>\n<p>September 2015: Erstellung<br>\nMrz 2017: Erweiterung<br>\nMrz 2018: Erweiterung</p>\n</div>\n<h2 id=\"weiterfuhrende-informationen\">Weiterfhrende Informationen</h2>\n<h3>Linksammlung</h3>\n<ul>\n<li><a href=\"https://github.com/Jolg42/awesome-typography\" target=\"_blank\">Awesome Typography</a></li>\n</ul>\n<h3>Schriften Testsen</h3>\n<ul>\n<li><a href=\"http://typecast.com\" target=\"_blank\">Typecast</a> (ber <a href=\"https://fonts.google.com/\" target=\"_blank\">Google Fonts</a> auch ohne Account)</li>\n</ul>\n<h3>Schriften erwerben</h3>\n<ul>\n<li><a href=\"https://www.myfonts.com/\" target=\"_blank\">MyFonts</a></li>\n<li><a href=\"https://typekit.com/\" target=\"_blank\">Typekit</a></li>\n<li><a href=\"https://www.fontshop.com/\" target=\"_blank\">FontShop</a></li>\n<li><a href=\"https://fontstand.com/\" target=\"_blank\">Fontstand</a></li>\n</ul>\n<h3>Freefonts</h3>\n<ul>\n<li><a href=\"http://www.fontsquirrel.com\" target=\"_blank\">Font Squirrel</a></li>\n<li><a href=\"https://fonts.google.com/\" target=\"_blank\">Google Fonts</a></li>\n<li><a href=\"http://www.dafont.com/\" target=\"_blank\">DaFont</a> (Qualitt zum Teil zweifelhaft)</li>\n</ul>\n<h3>Icon-Fonts erstellen</h3>\n<ul>\n<li><a href=\"http://fontello.com/\" target=\"_blank\">Fontello</a></li>\n</ul>\n<h3>Responsive Schriftgrssen</h3>\n<ul>\n<li><a href=\"https://css-tricks.com/viewport-sized-typography/\" target=\"_blank\">Viewport Sized Typography</a></li>\n<li><a href=\"http://www.w3.org/TR/css3-values/#viewport-relative-lengths\" target=\"_blank\">CSS Values and Units Module Level 3</a></li>\n<li><a href=\"http://codeitdown.com/responsive-font-size-css/\" target=\"_blank\">Responsive Font Size with CSS</a></li>\n</ul>\n<h3>Responsive Schriftanpassung</h3>\n<ul>\n<li><a href=\"http://alistapart.com/blog/post/variable-fonts-for-responsive-design\" target=\"_blank\">A List Apart  Variable Fonts for Responsive Design</a></li>\n<li><a href=\"http://alistapart.com/article/live-font-interpolation-on-the-web\" target=\"_blank\">A List Apart  Live Font Interpolation on the Web</a></li>\n<li><a href=\"http://alistapart.com/d/412/interpolation/\" target=\"_blank\">A List Apart  Live font interpolation</a></li>\n<li><a href=\"http://www.lucasfonts.com/about/interpolation-theory/\" target=\"_blank\">LucasFonts  Interpolation Theory</a></li>\n<li><a href=\"http://font-to-width.com/\" target=\"_blank\">FontToWidth Proof of Concept</a></li>\n<li><a href=\"https://css-tricks.com/examples/ViewportTypography/\" target=\"_blank\">Viewport Sized Typography</a></li>\n</ul>\n<h3>Fonts in Webfonts konvertieren</h3>\n<ul>\n<li><a href=\"http://www.fontsquirrel.com/tools/webfont-generator\" target=\"_blank\">Font Squirrel</a></li>\n</ul>\n<h3>Webfonts und Kompression von Webfonts</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Web_Open_Font_Format\" target=\"_blank\">Web Open Font Format - Wikipedia</a></li>\n<li><a href=\"http://www.w3.org/TR/WOFF/\" target=\"_blank\">WOFF File Format 1.0</a></li>\n<li><a href=\"https://www.w3.org/TR/WOFF2/\" target=\"_blank\">WOFF File Format 2.0</a></li>\n<li><a href=\"http://googledevelopers.blogspot.hu/2015/02/smaller-fonts-with-woff-20-and-unicode.html\" target=\"_blank\">Google Developers Blog  WOFF 2.0</a></li>\n</ul>\n"}

