	module.exports = {"title":"Lokalisierung  Internationalisierung","body":"<h1 id=\"lokalisierung-internationalisierung\">Lokalisierung  Internationalisierung</h1>\n<h2 id=\"intro\">Intro</h2>\n<style>\n\n\n.example {\n  background-color: #ddd;\n\n  padding-top: 0.7rem;\n  padding-right: 1.2rem;\n  padding-bottom: 0.9rem;\n  padding-left: 1.2rem;\n\n  margin-top: 1rem;\n  margin-bottom: .3rem;\n}\n\n.example--h1 {\n  font-size: 3rem;\n}\n\n.example--rtl {\n  text-align: right;\n}\n\n.example-wm.Text1 span, .example-wm.Text1 {\n  writing-mode: horizontal-tb;\n  -webkit-writing-mode: horizontal-tb;\n  -ms-writing-mode: horizontal-tb;\n}\n\n.example-wm.Text2 span, .example-wm.Text2 {\n  writing-mode: vertical-lr;\n  -webkit-writing-mode: vertical-lr;\n  -ms-writing-mode: vertical-lr;\n}\n\n.example-wm.Text3 span, .example-wm.Text3 {\n  writing-mode: vertical-rl;\n  -webkit-writing-mode: vertical-rl;\n  -ms-writing-mode: vertical-rl;\n}\n\n.example-wm.Text4 span, .example-wm.Text4 {\n  writing-mode: sideways-lr;\n  -webkit-writing-mode: sideways-lr;\n  -ms-writing-mode: sideways-lr;\n}\n\n.example-wm.Text5 span, .example-wm.Text5 {\n  writing-mode: sideways-rl;\n  -webkit-writing-mode: sideways-rl;\n  -ms-writing-mode: sideways-rl;\n}\n\n</style>\n<h3>Lokalisierung (l10n)</h3>\n<div class=\"margin\">\n<h4>l10n</h4>\n<p>Lokalisierung wird oft als l10n geschrieben. 10 gibt dabei die Anzahl der Buchstaben zwischen l und n im englischen Wort localization ist. Gesprochen wird jedoch von Lokalisierung/localization.</p>\n</div>\n<p>Mit Lokalisierung meint man den Vorgang, wie man Content und Produkte an einen Kulturkreis anpasst. Dazu knnen ganz verschiedene Anpassungen ntig sein.</p>\n<p>Beispiele fr Anpassungen bei der Lokalisierung:</p>\n<ul>\n<li>Sprache</li>\n<li>Whnrung</li>\n<li>Kulturelle Referenzen</li>\n<li>Farben</li>\n<li>Zeitangaben</li>\n</ul>\n<h3>Internationalisierung (i18n)</h3>\n<div class=\"margin\">\n<h4>l10n</h4>\n<p>Internationalisierung wird oft als i18n geschrieben. 18 gibt dabei die Anzahl der Buchstaben zwischen i und n im englischen Wort internationalization ist. Gesprochen wird jedoch von Internationalisierung/internationalization.</p>\n</div>\n<p>Bei der Internationalisierung geht es darum ein System oder Produkt so zu gestalten, dass spter eine Lokalisierung mglich ist.</p>\n<p>Einige Beispiele fr Internationalisierung</p>\n<ul>\n<li>Dimensionen ermglichen  Ein System so bauen, dass mehrere Dimensionen  wie Sprache oder Land  mglich wird</li>\n<li>Fallbacks  Was geschieht, wenn ich nicht alle Inhalte in allen Dimensionen habe?</li>\n<li>Templating  Inhalt und Programmcode von einander abspalten. Inhalt sollte unabhngig vom Prgorammcode editiert werden knnen</li>\n<li>Views  Der selbe Inhalt sollte je nach Kulturkreis anders angezeigt werden. Beispiel Datum.</li>\n</ul>\n<h2 id=\"text\">Text</h2>\n<h3>Sprache, Land und Kultur</h3>\n<div class=\"margin\">\n<ul>\n<li><a href=\"https://www.unicode.org/cldr/charts/33/summary/root.html\" target=\"_blank\">Lokalisierungen vom Unicode Konsortium</a></li>\n<li><a href=\"https://www.w3.org/International/techniques/authoring-html\" target=\"_blank\">W3C  Internationalization techniques: Authoring HTML &amp; CSS</a></li>\n</ul>\n</div>\n<p>Text ist fr die Lokalisierung meist in die jeweilige Sprache anzupassen. Jedoch gengt eine reine Sprachliche Anpassung nicht immer. So kann zum Beispiel in mehreren Lndern die selbe Sprache gelten aber andere Whrungen. Somit ist eine weitere Dimension zu Pflegen. Auch lokale Referenzen und Vergleiche werden nicht berall gleich verstanden.</p>\n<p>In einem Browser kann die Sprache ber grundstzlich drei Faktoren gewhlt werden:</p>\n<ul>\n<li>Geolokalisierung aufgrund der IP (ungenau)</li>\n<li>Sprache aufgrund der Browsersprache</li>\n<li>Gemss Auswahl des Kunden (Voreinstellung oder Sprachwahl)</li>\n</ul>\n<div class=\"margin\">\n<h4>Dimensionen</h4>\n<p>Beispiel einer Website um Flge zu buchen. Es gibt drei Dimensionen der Lokalisierung:</p>\n<ul>\n<li>Region</li>\n<li>Sprache</li>\n<li>Whrung</li>\n</ul>\n</div>\n<p><img src=\"./img/text/skyscanner.png\" alt=\"\"></p>\n<div class=\"margin\">\n<h4>Individualisierung</h4>\n<p>Beispiel zur Konfiguration des MacOS 10.14 Betriebsystem</p>\n</div>\n<p><img src=\"./img/text/macOS_10.14.png\" alt=\"\"></p>\n<h2 id=\"unicode\">Unicode</h2>\n<div class=\"margin\">\n<h3>Vorlufer</h3>\n<ul>\n<li>Diverse Standards fr verschiedene Betriebsysteme und Sprachregionen</li>\n<li><a href=\"http://ascii-table.com/\" target=\"_blank\">ASCII</a> diente fr die ersten 127 Zeichen des spteren Unicode</li>\n</ul>\n<h3>Unicode Version 1 <br />(Oktober 1991)</h3>\n<p>7161 Zeichen fr insgesammt 24 Schriftsysteme wurden standardisiert.</p>\n<h3>Unicode Version 12 <br />(Mrz 2019)</h3>\n<p>137993 Zeichen fr insgesammt 150 Schriftsysteme wurden standardisiert.</p>\n</div>\n<p>Das <a href=\"http://www.unicode.org/\" target=\"_blank\">Unicode Konsortium</a> wurde geschaffen um eine Standardisierung zu erreichen, welcher Wert/Zeichencode (Codepoint) welchem Schriftzeichen (Glyph) entspricht.</p>\n<h3>Beispiel</h3>\n<ul>\n<li>Wert 65 entspricht einem lateinischen A (Grossbuchstabe)</li>\n<li>Notation meist in Hex <code>U+0041</code></li>\n<li>In HTML in DEC <code>&amp;#65;</code> oder HEX <code>&amp;#x41;</code> geschrieben.</li>\n<li><a href=\"http://unicode.org/cldr/utility/character.jsp?a=0041\" target=\"_blank\">Unicode Eintrag fr A</a></li>\n</ul>\n<h3>Encoding</h3>\n<p>Beim Encoding wird festgehalten, wie der Codepoint abgespeichert wird.</p>\n<h3>Verbreitete Encodings fr Unicode</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/UTF-8\" target=\"_blank\">UTF-8</a></li>\n<li>UTF-16 (nur verwenden, wenn UTF-8 nicht mglich)</li>\n<li>UTF-32 (nur verwenden, wenn UTF-8 nicht mglich)</li>\n</ul>\n<h3>Beispiel</h3>\n<ul>\n<li>A = BIN <code>01000001</code> in UTF-8 Encoding</li>\n</ul>\n<h3>Falsches Encoding</h3>\n<div class=\"example example--h1\">\nWrter  Wrter\n</div>\nEncoding Windows Latin 1 als Mac OS Roman interpretiert.\n<div class=\"margin\">\n<h3>Noto</h3>\n<p>Der <a href=\"https://www.google.com/get/noto/\" target=\"_blank\">Google-Font Noto</a> hat das Ziel alle Zeichen des Unicode-Standards in einem Font zugnglich zu machen. Der Name des Fonts sthet fr fr No Tofu. Wobei Tofu hier den replacement character  meint.</p>\n</div>\n<div class=\"example example--h1\">\nWrter  Wrter\n</div>\n<p>Wenn das Zeichen korrekt Encoded ist, jedoch der Font kein Glyph fr das Zeichen hat, so werden so genannte replacement characters gezeigt.</p>\n<h2 id=\"ubersetzungen\">bersetzungen</h2>\n<p>bersetzungen werden meist gebraucht um eine verschriftliche Sprache in eine andere zu berfhren. Dies kann heute durch Menschen oder durch Maschinen gemacht werden. Maschinelle bersetzungen sollten nur dort gebraucht werden, wo es keine Mglichkeit gibt einen Menschen mit dieser Aufgabe zu betrauen, da maschinelle bersetzungen noch immer Fehlerhaft sind.</p>\n<h2 id=\"schreibweise\">Schreibweise</h2>\n<p>Je nach Region knnen sich einzelne Wrter oder Schreibweisen durchgesezt haben. Dies ist bei einer Lokalisierung zwinden zu bercksichtigen. Nachfolgend einige Beispiele.</p>\n<h3> oder ss</h3>\n<div class=\"margin\">\n<ul>\n<li>Infos zur Unicode <a href=\"http://unicode.org/reports/tr15/\" target=\"_blank\">Normalisierung</a> von   = ss</li>\n</ul>\n</div>\n<div class=\"example example--h1\">\nGrsse: 80m\n</div>\nEine Schreibweise ohne  wird in der Schweiz als normal erachtet. In Deutschland wird dies als Schreibfehler wahrgenommen.\n<div class=\"example example--h1\">\nGre: 80m\n</div>\nEine Schreibweise mit  wird in Deutschland aber auch in der Schweiz als normal erachtet.\n<h3>US oder UK</h3>\n<div class=\"example example--h1\">\ncolor gray\n</div>\nDie US-Schreibweise wird hufig auch ausserhalb von den USA in internationalen Medien verwendet.\n<div class=\"example example--h1\">\ncolour grey\n</div>\nDie UK-Schreibweise wird hufig in UK aber zum Teil auch im akademischen Umfeld benutzt.\n<h2 id=\"vergleiche-bezuge\">Vergleiche &amp; Bezge</h2>\n<p>Vergleiche sind sehr individuell fr eine Kulturform und knnen nicht global verstanden werden. Sie unterscheiden Sich sogar innerhalb einer Kulturform zum gleich Massiv.</p>\n<h3>Grssenvergleiche</h3>\n<p>Oft in populren Medien wird zum Veranschaulichen eine nicht <a href=\"https://de.wikipedia.org/wiki/Internationales_Einheitensystem\" target=\"_blank\">SI-Norm</a> verwendet.</p>\n<div class=\"margin compact\">\n<h4>Saarland</h4>\n<p>In Medien aus Deutschland oft mit der Flcheneinheit Saarland ein Vergleich gemacht. Im Nachbarland Schweiz ist hingengen nicht einmal allen Menschen  klar, dass Saarland ein Bundesland in Deutschland ist und sie haben kein Gefhl fr dessen grsse.</p>\n</div>\n<div class=\"example example--h1\">\n ist etwa doppelt so gro wie das Saarland.\n</div>\n~ 2570km\n<h3>Vorwissen</h3>\n<p>Je nach Publikum und Alter haben die Konsumenten eine andere Vorbildung und gewisse Bezge funktionieren nicht mehr.</p>\n<div class=\"margin compact\">\n<h4>Geschichte</h4>\n<p>Meist sind geschichtliche Referenzen nur dann zu verstehen, wenn Sie sehr bekannt sind oder in der eigenen Lebenszeit erlebt wurden.</p>\n</div>\n<div class=\"example example--h1\">\nNoch whrend der chinesischen Kulturrevolution \n</div>\n~ 19661976\n<div class=\"example example--h1\">\nNoch whrend dem 2.Weltkrieg \n</div>\n~ 19391945\n<h2 id=\"typografie\">Typografie</h2>\n<div class=\"margin\">\n<ul>\n<li><a href=\"https://github.com/frankrausch/Typographizer\" target=\"_blank\">Typographizer</a> korrigiert Anfhrungszeichen.</li>\n</ul>\n</div>\n<p>Die typografischen Regeln ndern sich von Land zu Land. Ein Beispiel sind Anfhrungs- und Schlusszeichen. Aber auch sonstige Regeln knnen in verschiedenen Lndern anders interpretiert werden.</p>\n<h3>Schweiz</h3>\n<p>In der Schweiz werden die Anfhrungszeichen mit den so genannten Guillemets geschrieben:</p>\n<div class=\"example example--h1\">\nHallo Welt!\n</div>\n<p>Kommen innerhalb der Anfhrungszeichen weitere Anfhrungen vor, so kommen die einfachen Anfhrungszeichen zur Anwendung:</p>\n<div class=\"example example--h1\">\nIch lese die Site Typo-Nerd tglich.\n</div>\n<h3>Deutschland</h3>\n<div class=\"example example--h1\">\nHallo Welt!  \n</div>\n<div class=\"example example--h1\">\nIch lese die Site Typo-Nerd tglich.\n</div>\n<h3>Franzsisch</h3>\n<div class=\"example example--h1\">\nBonjour!  \n</div>\n<div class=\"example example--h1\">\nJean a dit: C'est la morale du film\n</div>\n<h3>Englisch</h3>\n<p>In der englischen Sprache kommen die Double quotes zum Einsatz.</p>\n<div class=\"example example--h1\">\nThe best is yet to come.\n</div>\n<p>oder aber auch die Single quotes</p>\n<div class=\"example example--h1\">\nThe best is yet to come.\n</div>\n<h3>Laufrichtung des Textes</h3>\n<p>Je nach Sprache kann der Text in ganz verschiedene richtungen laufen. In den westlichen Sprachen wird hufig von links nach rechts gelesen. Entsprechend der Leserichtung ist auch die Abfolge der Elemente zu sortieren.</p>\n<h3>left to right (ltr) und right to left (rtl)</h3>\n<p>Hufig wird von ltr oder rtl Sprachen gesprochen. Diese Abkrzungen stehen fr die Anfangsbuchstaben des englischen left to right und right to left.</p>\n<div class=\"example example--h1 example--rtl\">\n\n</div>\nHebrisch ist eine hufig verwendete rtl Sprache (Wortbedeutung: Hebrisch)\n<h3>vertikale Laufrichtungen</h3>\n<p>In heutigen Browsern lassen sich auch vertikale Laufrichtungen realisieren. Jedoch untersttzt nicht jeder Browser jeden Modus.</p>\n<div class=\"margin\">\n<p>Beispiel von <a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/writing-mode\" target=\"_blank\">MDN</a></p>\n</div>\n<table>\n  <tr>\n    <th>CSS-Wert</th>\n    <th>Vertikales Schriftystem</th>\n    <th>Horizontales Schriftystem</th>\n    <th>Gemischte Schriftysteme</th>\n  </tr>\n  <tr>\n    <td>horizontal-tb</td>\n    <td class=\"example-wm Text1\"><span></span></td>\n    <td class=\"example-wm Text1\"><span>Example text</span></td>\n    <td class=\"example-wm Text1\"><span>1994</span></td>\n  </tr>\n  <tr>\n    <td>vertical-lr</td>\n    <td class=\"example-wm Text2\"><span></span></td>\n    <td class=\"example-wm Text2\"><span>Example text</span></td>\n    <td class=\"example-wm Text2\"><span>1994</span></td>\n  </tr>\n  <tr>\n    <td>vertical-rl</td>\n    <td class=\"example-wm Text3\"><span></span></td>\n    <td class=\"example-wm Text3\"><span>Example text</span></td>\n    <td class=\"example-wm Text3\"><span>1994</span></td>\n  </tr>\n  <tr>\n    <td>sideways-lr</td>\n    <td class=\"example-wm Text4\"><span></span></td>\n    <td class=\"example-wm Text4\"><span>Example text</span></td>\n    <td class=\"example-wm Text4\"><span>1994</span></td>\n  </tr>\n  <tr>\n    <td>sideways-rl</td>\n    <td class=\"example-wm Text5\"><span></span></td>\n    <td class=\"example-wm Text5\"><span>Example text</span></td>\n    <td class=\"example-wm Text5\"><span>1994</span></td>\n  </tr>\n</table>\n<h2 id=\"sortierung\">Sortierung</h2>\n<div class=\"margin\">\n<ul>\n<li><a href=\"https://www.unicode.org/reports/tr10/tr10-38.html\" target=\"_blank\">Unicode Sortierung</a></li>\n</ul>\n</div>\n<p>Fr die Sortierung von Eintrgen gibt es in ganz verschiedenen Kulturkreisen verschiedene Regeln. Sortierungen mssen von technischen Systemen untersttz werden, da diese meist Automatisch gemacht werden.</p>\n<h3>Deutscher Sprachraum</h3>\n<div class=\"margin\">\n<p>Beispiele aus <a href=\"https://de.wikipedia.org/wiki/Alphabetische_Sortierung#Beispiel_f%C3%BCr_deutschsprachige_Sortierungen\" target=\"_blank\">Wikipedia</a></p>\n</div>\n<p>Im deutschen Sprachraum alleine gibt es bereits drei Sortierungen.</p>\n<ul>\n<li>DIN 5007 Variante 1 = Lexikon</li>\n<li>DIN 5007 Variante 2 = Telefonbuch</li>\n<li>sterreichische Sortierung (Telefonbuch)</li>\n</ul>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<div class=\"example example--h1\">\n<h3>DIN 5007 Variante 1</h3>\n<ul>\n<li>Gbel</li>\n<li>Goethe</li>\n<li>Goldmann</li>\n<li>Gthe</li>\n<li>Gtz</li>\n</ul>\n</div>\n<p><strong> = o</strong></p>\n</div>\n<div class=\"col_4of12\">\n<div class=\"example example--h1\">\n<h3>DIN 5007 Variante 1</h3>\n<ul>\n<li>Gbel</li>\n<li>Goethe</li>\n<li>Gthe</li>\n<li>Gtz</li>\n<li>Goldmann</li>\n</ul>\n</div>\n<p><strong> = oe</strong></p>\n</div>\n<div class=\"col_4of12_last\">\n<div class=\"example example--h1\">\n<h3>DIN 5007 Variante 1</h3>\n<ul>\n<li>Goethe</li>\n<li>Goldmann</li>\n<li>Gbel</li>\n<li>Gthe</li>\n<li>Gtz</li>\n</ul>\n</div>\n<p><strong> folgt auf o</strong></p>\n</div>\n</div>\n<h2 id=\"personennamen-titel\">Personennamen &amp; Titel</h2>\n<p>In vielen westlichen Lndern werden akademische Grade oder Auszeichnungen  wie zum Beispiel Doktor oder Hofrat (in stereich gebruchlich fr hhere Beamte)  im Namen gefhrt. Da gewise Personen einen Wert darauf legen, sollte dies jeweils bercksichtigt werden.<br>\nIn sterreich gibt es zum Beispiel <a href=\"https://karriere.sn.at/karriere-ratgeber/fort-weiterbildung/wo-der-titel-regiert-28942096\" target=\"_blank\">ber 1500</a> Titel die per Gesetz geregelt sind.</p>\n<p><img src=\"./img/text/titel-small.png\" alt=\"\"></p>\n<p>Beispiel der <a href=\"https://www.wiener-staatsoper.at/\" target=\"_blank\">Wiener Staatsoper</a></p>\n<h2 id=\"masseinheiten\">Masseinheiten</h2>\n<p>Ausser Myanmar, Liberia und die USA haben alle anderen Lnder das <a href=\"https://de.wikipedia.org/wiki/Internationales_Einheitensystem\" target=\"_blank\">internationale Einheitensystem (SI)</a>. Da jedoch die USA im internationealen Umfeld eine wichtige Rolle spielt und diverse Internet-Dienste US-basiert sind, gilt es einige Besonderheiten im Zusammenhang mit den US-Einheiten zu beachten.</p>\n<h3>Lngen</h3>\n<div class=\"margin compact\">\n<h4>US Lngenmass</h4>\n<ul>\n<li>1 Punkt (DTP) = ~0.3527mm</li>\n<li>72 Punkt (DTP) = 1 Zoll</li>\n<li>1 Zoll = 2.54cm</li>\n<li>1 Fuss = 12 Zoll = 30.48cm</li>\n<li>1 Meile = 63360 Zoll = ~1.61km</li>\n</ul>\n</div>\n<p>In den USA werden kleinere Lngen in Zoll (2.54cm) oder Fuss (30.48cm) angegeben. Grssere Distanzen werden in Meilen (~1.61km) gemessen. Dies fhrt dazu, dass Amerikaner in der tglichen Konversation eine kleinere Auflsung von Lngeneinheiten angeben. Beispiel: Viele Menschen in Europa kennen ihre Krpergrsse auf 1cm genau. Viele Amerikaner nur auf  Fuss genau oder maximal einen Zoll.</p>\n<h3>Papiergrssen</h3>\n<p>Beim Ausdrucken im US-Raum muss davon ausgegangen werden, dass die Ausgabe auf dem Format US-Letter erfolgt und nicht im sonst blichen A4.</p>\n<div class=\"margin compact\">\n<h4>US-Letter</h4>\n<ul>\n<li>8.511 Zoll =  215.9279.4mm</li>\n</ul>\n<h4>A4</h4>\n<ul>\n<li>210297mm</li>\n</ul>\n</div>\n<p><img src=\"./img/SVG/letter-size.svg\" alt=\"Papiergrssen\"></p>\n<h2 id=\"temperatur\">Temperatur</h2>\n<p>In den meisten Regionen der Welt wird Grad Celsius (C) zur Temperaturangabe benutzt in den USA wird Grad Fahrenheit (F) verwendet. Eine entsprechende Umrechnung muss fr die jeweilige Lokalisierung gemacht werden.</p>\n<h2 id=\"zeit\">Zeit</h2>\n<p>In vielen Kulturregionen der Welt wird ein Tag (eine Rotation um die Erdachse) in 24 Stunden eingeteilt. Die Stunden wiederum in 60 Minuten. Andere weit weniger verbreitete und beinahe unbekannte Systeme sind <a href=\"https://en.wikipedia.org/wiki/Decimal_time\" target=\"_blank\">dezimale Systeme</a> wie zum Beispiel die <a href=\"https://en.wikipedia.org/wiki/Swatch_Internet_Time\" target=\"_blank\">Swatch Internet Time</a>.</p>\n<h3>24-Stunden oder AM/PM</h3>\n<p>In einigen Regionen der Welt wird anstatt der 24-Stunden-Schreibweise fr die Zeit ein 12-Stunden-Schreibweise mit AM (ante meridies, vor Mittags) und PM (post meridies, nach Mittags) als Zusatz geschrieben. Dies fhr vor allem bei Mitternacht und Mittags oftmals zu verwirrung;</p>\n<table>\n<thead>\n<tr>\n<th>AM/PM</th>\n<th><a href=\"https://en.wikipedia.org/wiki/ISO_8601\" target=\"_blank\">ISO 8601</a></th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>11:59 AM</td>\n<td>11:59</td>\n</tr>\n<tr>\n<td>12:00 PM</td>\n<td>12:00</td>\n</tr>\n<tr>\n<td>11:59 PM</td>\n<td>23:59</td>\n</tr>\n<tr>\n<td>12:00 AM</td>\n<td>24:00</td>\n</tr>\n</tbody>\n</table>\n<p>Wir die AM/PM schreibweise verwendet, sollten keine fhrenden Nullstellen bei der Stunde verwendet werden um <a href=\"https://twitter.com/LeaVerou/status/1229481014906511360\" target=\"_blank\">Verwirrungen</a> zu vermeiden. Also nicht <s>02:45 PM</s> sondern 2:45 PM. Aber immer fhrende Nullstellen bei den Minuten.</p>\n<h3>Schaltsekunde</h3>\n<p>Da die Erde leicht langsamer um die eigene Achse rotiert, als die definierte Anzahl Sekunden (86400) in einem Tag (Sekunde zu schnell definiert), wird mit Schaltsekunden diese Abweichung kompensiert.</p>\n<p>Der Internationale Dienst fr Erdrotation und Referenzsysteme legt fest, wann eine zustzliche Schaltsekunde eingefgt werden muss. Dies ist in unregelmssigen abstnden und an unterschiedlichen Daten (meist 30.Juni oder 31.Dezember) der Fall.</p>\n<h4>Beispiel</h4>\n<pre><code class=\"language-md\">31.12.2011 23:59:59\n31.12.2011 23:59:60  Schaltsekunde\n01.01.2012 00:00:00\n</code></pre>\n<br>\n<p>Es gibt diverse Probleme mit Computer-Systemen und Schaltsekunden. <a href=\"https://aws.amazon.com/blogs/aws/look-before-you-leap-december-31-2016-leap-second-on-aws/\" target=\"_blank\">Einige Firmen</a> sind dazu bergegangen, die Schaltsekunde ber einen Tag zu verteilen um keine einundsechzigste Sekunde in einer Stunde haben zu mssen.</p>\n<h3>Zeitzonen</h3>\n<p>Durch die Erdrotation werden verschiedene Regionen der Welt zu unterschiedlichen Zeiten der Sonne zugewandt (Tag). Die Zeitmessung wird jedoch meist daran gerichtet, dass man um Mittag den hchsten Sonnenstand mchte. Somit muss die Abweichung ber so genannte Zeitzonen kompensiert werden:</p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/8/88/World_Time_Zones_Map.png\" alt=\"Zeitzonen\">\nQuelle: <a href=\"https://en.wikipedia.org/wiki/File:World_Time_Zones_Map.png\" target=\"_blank\">Wikipedia</a></p>\n<p>Einige Regionen und politische Einheiten sind trotz grosser Ausdehnung in den Lngengraden als einheitliche Zeitzone zusammengefasst. So ist zum Beispiel ganz China in der gleichen Zeitzone (UTC+8). Dadurch ergibt sich beim Grenzberschritt nach Afganistan der grsste Zeitsprung der Erde von -3.5 Stunden.</p>\n<p>Es kann vorkommen, dass Zeitzonen unlogisch und schnell ndern. Beispiel hierfr ist Australien, welches im Jahr 2000 fr die dauer der Olymischen Sommerspiele und im Jahr 2006 fr die Commonwealth Games ihre Zeitzonen vereinheitlichte.</p>\n<h3>UTC  Coordinated Universal Time</h3>\n<p>Seit 1884 wird der <a href=\"https://de.wikipedia.org/wiki/Nullmeridian\" target=\"_blank\">Nullmeridian</a> basierend auf der Ortschaft Greenwich (England) verwendet um die Zeitmessung international standardisiert zu koordinieren. Seit 1972 wird dabei von der Koordinierten Weltzeit (UTC  Coordinated Universal Time) gesprochen. Davor wurde der bis heute in England gebruchliche Ausdruck Greenwich Mean Time (GMT) verwendet.</p>\n<div class=\"margin compact\">\n<p>Quelle: <a href=\"https://de.wikipedia.org/wiki/Nullmeridian\" target=\"_blank\">Wikipedia</a></p>\n</div>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/e/e2/Breitenkreis-und-Nullmeridian.png\" alt=\"Nullmeridian\"></p>\n<h3>CET  Central European Time</h3>\n<p>Die mitteleuropische Zeit (MEZ) oder Central European Time (CET) ist die am meisten gebrauchte Zeitzone in Europa. Sie weist zur UTC-Zeit eine Offset von plus einer Stunde auf. Deshalb kann sie auch als UTC+1 geschrieben werden (siehe Sommerzeit).</p>\n<div class=\"margin compact\">\n<p>Quelle: <a href=\"https://de.wikipedia.org/wiki/Mitteleurop%C3%A4ische_Zeit\" target=\"_blank\">Wikipedia</a></p>\n</div>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/7/71/Time_zones_of_Europe.svg\" alt=\"\">\nRegionen mit mitteleuropische Zeit (MEZ) in rot markiert.</p>\n<h3>Zeitzonen der USA</h3>\n<p>Die USA hat <a href=\"https://de.wikipedia.org/wiki/Zeitzonen_in_den_Vereinigten_Staaten\" target=\"_blank\">insgesammt 11 Zeitzonen</a>. Wobei meist nur von den vier Zeitzonen auf dem zusammenhngenden Festlandgebiet gesprochen wird:</p>\n<ul>\n<li>Eastern Time Zone  UTC05:00</li>\n<li>Central Time Zone  UTC06:00</li>\n<li>Mountain Time Zone  UTC07:00</li>\n<li>Pacific Time Zone  UTC08:00</li>\n</ul>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/3/32/US-Timezones-post-2007.png\" alt=\"\">\nQuelle: <a href=\"https://en.wikipedia.org/wiki/File:US-Timezones-post-2007.png\" target=\"_blank\">Wikipedia</a></p>\n<h3>Computer &amp; Zeitzonen</h3>\n<p>Auf vielen Servern wird bei der Installation keine automatische Zeitzone gestellt. Somit haben viele Server UTC als Standard. Bei Desktop-Computern wird meist mit Geolokation versucht die richtige Zeitzone zu finden.</p>\n<h3>Sommerzeit</h3>\n<p>In einigen Regionen der Welt wird in den Sommermonaten eine Sommerzeit (Daylight saving time  DST) eingefhrt. Diese wird meist eine Stunde vor der blichen Zeit gestellt. So ist die mitteleuropische Zeit (MEZ) in den Wintermonaten UTC+1 und in den Sommermonaten UTC+2.</p>\n<div class=\"margin compact\">\n<h4>Schweiz</h4>\n<p>Am 28. Mai 1978 wurde in einer Volksabstimmung (Referendum) beschlossen, dass man sich nicht mit der Sommerzeit den Nachbarstaaten anschliesst. Aufgrund massiver Probleme wurde die Zeitinsel-Schweiz per Gesetz ab 1. Januar 1981 dem Ausland angeglichen und das Chaos hatte ein Ende.<br>\nQuelle: <a href=\"https://en.wikipedia.org/wiki/Daylight_saving_time\" target=\"_blank\">Wikipedia</a></p>\n</div>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/1/16/DST_Countries_Map.png\" alt=\"\">\nRegionen mit Sommerzeit in blau markiert. Bildquelle: <a href=\"https://en.wikipedia.org/wiki/Daylight_saving_time\" target=\"_blank\">Wikipedia</a></p>\n<p>Politische Entscheide knnen sehr schnell die Einfhrung oder Abschaffung von Sommerzeit beeinflussen. <a href=\"https://www.maghreb-post.de/gesellschaft/marokko-regierung-schafft-die-zeitumstellung-ab/\" target=\"_blank\">In Maroko</a> wurde zum Beispiel am 27. Oktober 2018 beschlossen, dass die Winterzeit am Folgetag nicht mehr zur Anwendung kommen wird. Dies fhrte zu diversen Problemen, da die Umstellung sehr kurzfristig war.</p>\n<h4>Abschaffung in der EU</h4>\n<p>Das EU-Parlament entschied im Mrz 2019, dass nur noch bis 2021 eine Sommerzeitumstellung erfolgen soll. Danach steht es den Mitgliedstaaten frei ob sie in Normal- oder Sommerzeit ihre weiter fahren. Es bleibt zu hoffen, dass die Staaten sich einigen knnen, so dass es weiterhin eine grosse geschlossene Zeitzone (MEZ) in der EU gibt.</p>\n<h2 id=\"datum-kalender\">Datum &amp; Kalender</h2>\n<p>Der <a href=\"https://de.wikipedia.org/wiki/Gregorianischer_Kalender\" target=\"_blank\">gregorianische Kalender</a> ist weltweit der meist gebrauchte Kalender und wurde im Jahr 1582 von Papst Gregor XIII verordnet. Dabei hat das Jahr (Umlauf der Erde um die Sonne) im Durchschnitt etwa 365,25 Tage und diese werden auf 12 Monate verteilt. Die Schaltjahre haben 366 Tage, sonstige Jahre werden mit 365 Tagen gefhrt.</p>\n<h3>Regeln fr Schaltjahre</h3>\n<ul>\n<li>1. Regel: Jahreszahl durch vier ganzzahlig teilbaren = Schaltjahr</li>\n<li>2. Regel: Jahreszahl durch 100 ganzzahlig teilbaren = kein Schaltjahr (1. Regel berlegen)</li>\n<li>3. Regel: Jahreszahl durch 400 ganzzahlig teilbaren = Schaltjahr (2. Regel berlegen)</li>\n</ul>\n<h3>Jahr-2000-Problem</h3>\n<p>Im Jahr 2000 gab es zwei Probleme; Das kleinere Problem war, dass diverse Software das Schaltjahr im Jahr 2000 nicht korrekt berechnet hatten und flschlich keine Schaltjahr annahmen. Das grssere Problem war, dass teils Jahreszahlen nur zweistellig abgespeichert wurden. Also anstatt zum Beispiel 1999 wurde nur 99 abgespeichert. Beim Jahreswechsel wurde somit neu 00 abgespeichert, was anstatt 2000 als 1900 interpretiert wurde.</p>\n<h2 id=\"numerische-notation-von-daten\">Numerische Notation von Daten</h2>\n<p>Bei der numerischen Schreibweise fr ein Datum gibt es diverse Standards. Es muss die jeweilige Konvention passend zur Anwendung und zum Land bercksichtigt werden.</p>\n<table>\n<thead>\n<tr>\n<th>Standard</th>\n<th>Format</th>\n<th>Beispiel</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>USA</td>\n<td>MM/DD/YYYY</td>\n<td>03/29/2000</td>\n</tr>\n<tr>\n<td>ISO 8601</td>\n<td>YYYY-MM-DD</td>\n<td>2000-03-29</td>\n</tr>\n<tr>\n<td>DIN 5008</td>\n<td>YYYY-MM-DD oder DD.MM.YYYY</td>\n<td>2000-03-29 / 29.3.2000</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"unix-timestamp\">Unix-Timestamp</h2>\n<p>In Computer-Systemen wird hufig eine Zeit als Unix time gespeichert. Dabei handelt es sich um eine Ganzzahl (signed integer) welche die vergangenen Sekunden seit 1.Januar 1970 um 0:00 Uhr rpresentiert. Die Zahl kann ein Vorzeichen haben, also auch einen Minus-Bereich abdecken und somit Ereignisse vor 1970 reprsentieren.</p>\n<h3>Beispiele</h3>\n<ul>\n<li>31.Dezember 1999, 23:59:59 = 946684799000</li>\n<li>22.November 1963, 18:30:00 = 192778200</li>\n</ul>\n<h3>Jahr-2038-Problem</h3>\n<p>Im Januar 2038 wird die Sekundenzahl so gross werden, dass die Zahl nicht mehr mit 32 Bit (vorzeichenbehaftete 32-Bit-Ganzzahl) gespeichert werden kann. Einige Computer-Systeme speichern jedoch noch immer in 32 Bit ab. Diese Systeme mssen bis sptestens dahin umgestellt werden. Neuere Systeme speichern Timestamps als 62-Bit-Wert ab. Somit kann noch fr die nchsten paar Milliarden Jahre die Zahl problemlos abgespeichert werden.</p>\n<div class=\"margin compact\">\n<p>Quelle: <a href=\"https://de.wikipedia.org/wiki/Jahr-2038-Problem\" target=\"_blank\">Wikipedia</a></p>\n</div>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/e/e9/Year_2038_problem.gif\" alt=\"\"></p>\n<h2 id=\"zahlungsverkehr\">Zahlungsverkehr</h2>\n<h2 id=\"besteuerung\">Besteuerung</h2>\n<p>Viele Lnder haben eine Steuer auf verkaufte Gter/Dienstleistung. Grundstzlich kann davon ausgegangen werden, dass die Steuer im Land des Konsumenten/Kufers erhoben werden muss.</p>\n<h2 id=\"mehrwertsteuer-in-der-schweiz-mwst\">Mehrwertsteuer in der Schweiz (MwSt)</h2>\n<p>In der Schweiz gibt es drei Mehrwertsteuerstze. Je nach Produkt/Dienstleistung muss man einen der Stze versteuern oder ist befreit von der Mehrwertsteuer. Bei Online-Shops gelten die selben Regeln wie an physischen Verkaufsstellen. Es ist empfehlenswert die Gesamtsumme inklusive Mehrwertsteuer anzugeben und dann je Mehrwertsteuer-Satz diese separat auszuweisen.</p>\n<h3>Beispiel</h3>\n<p><img src=\"./img/mwst.png\" alt=\"MwSt\"></p>\n<h2 id=\"bankverkehr\">Bankverkehr</h2>\n<p>Heute wird im internationalen Bankverkehr hufig elektronisch Geld bermittelt. Dabei werdne hufig die Konten mittels IBAN (International Bank Account Number) angegeben. In Deutschland zum Beispiel ist die IBAN als ein Geheimnis zu wahren, da mittels IBAN Geld von einem Konto angefordert werden kann. Somit sollte diese Information nicht unbedingt ffentlich gemacht werden.</p>\n<h2 id=\"checks\">Checks</h2>\n<p>In den USA werden Bankberweisungen eher selten gemacht. Man schickt seinem Geschftspartner einen Check, der dann berechtigt vom eigenen Konto den Betrag des Checks zu beziehen.</p>\n<h2 id=\"paypal\">PayPal</h2>\n<p>PayPal ist eines der am weitesten verbreiteten rein elektronischen Zahlungsmittel. PayPal ist relativ einfach in Shop-Systeme zu integrieren und auch Personen ohne PayPal-Konto knnen dann direkt via Kreditkarte bezahlen.</p>\n<h2 id=\"kreditkarten\">Kreditkarten</h2>\n<p>Um im eigenen Shop mit Kreditkarten zu bezahlen wird meist ein Kreditkarten-Prozessor eingesetzt. Diese Firmen haben Vertrge mit den Kreditkartenbetreibern und wickeln den eigentlichen Zahlungsvorgang ab. Als Website-Betreiber kann ich zwischen diversen Betreibern whlen. Hier ein paar grosse Vertreter:</p>\n<ul>\n<li><a href=\"https://www.six-payment-services.com/en/site/e-commerce/solutions/paymentsolution.html\" target=\"_blank\">Saferpay/SIX</a> (im Schweizer Markt)</li>\n<li><a href=\"https://www.datatrans.ch/\" target=\"_blank\">datatrans</a></li>\n<li><a href=\"https://stripe.com/ch\" target=\"_blank\">Stripe</a></li>\n<li><a href=\"https://www.shopify.de/zahlungsportal/schweiz\" target=\"_blank\">shopify</a></li>\n</ul>\n<p>Es fallen im allgemeinen Gebren an, die der Shop-Betreiber zu bezahlen hat.</p>\n<h2 id=\"twint\">Twint</h2>\n<p>In der Schweiz gibt es einen breiten Bankenzusammenschluss fr das elektronische Zahlungsmittel Twint. Dieses kann relativ einfach in bestehende Shops integriert werden. Die Bezahlung erfolgt mittels einem QR-Code, der ber die App am Smartphone gescannt und besttigt werden kann.</p>\n<h2 id=\"apple-pay-google-pay\">Apple-Pay &amp; Google Pay</h2>\n<p>Neuere Smartphones habe in Hardware die Funktion einer Kreditkarte abgebildet. Somit knnen diese an vielen Bezahlstellen, wo auch kontaktlose Bezahlung mglich ist, verwendet werden.</p>\n<h2 id=\"geografie\">Geografie</h2>\n<h3>Kartenprojektion</h3>\n<div class=\"margin\">\n<h4>Swiss Grid</h4>\n<p>Die Schweizerische Landesvermessung verwendet fr die Projektion der Schweiz blicherweise die Projektonsart <a href=\"https://www.swisstopo.admin.ch/de/wissen-fakten/geodaesie-vermessung/bezugsysteme/kartenprojektionen.html\" target=\"_blank\">Swiss Grid</a>.</p>\n<ul>\n<li><a href=\"https://observablehq.com/@rzoller/swiss-grid-vs-web-mercator\" target=\"_blank\">Visualisierung Swiss Grid vs. Web Mercator</a></li>\n<li><a href=\"http://richarddmorey.org/map/\" target=\"_blank\">Weitere alternative Projektionen von Karten</a></li>\n</ul>\n</div>\n<p>Die am weitesten Verbreitete Projektion der runden Erde auf eine zweidimensionale Karte ist die so genannte <a href=\"https://de.wikipedia.org/wiki/Mercator-Projektion\" target=\"_blank\">Mercator-Projektion</a>:</p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/f/fa/Mercator-proj.png\" alt=\"Mercator-Projektionen\">\nQuelle: <a href=\"https://de.wikipedia.org/wiki/Mercator-Projektion#/media/File:Mercator-proj.png\" target=\"_blank\">Wikipedia</a></p>\n<p>Zu beachten ist jedoch, dass die Darstellung eine Verzerrung der Grssenverhltnisse ist. Hier die grssenverhltnisse je Land korrigiert:</p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/e/ee/Worlds_animate.gif\" alt=\"\"></p>\n<p>Quelle: <a href=\"https://en.wikipedia.org/wiki/Mercator_projection#/media/File:Worlds_animate.gif\" target=\"_blank\">Wikipedia</a></p>\n<p>Es gibt jedoch noch viele ander <a href=\"https://en.wikipedia.org/wiki/List_of_map_projections\" target=\"_blank\">Kartenprojektionen</a>, welche das <a href=\"https://xkcd.com/977/\" target=\"_blank\">Problem</a> einer Abwicklung einer Kugel versuchen mglichst gut zu lsen.</p>\n<h2 id=\"postadressen\">Postadressen</h2>\n<p>Bei Postanschriften ist die jeweilige lokale Schreibweise zu verwenden.</p>\n<h3>Beispiel fr England</h3>\n<pre><code class=\"language-md\">Name\n159 Brick Lane\nLondon E1 6SB\n</code></pre>\n<h3>Beispiel fr USA</h3>\n<pre><code class=\"language-md\">Name\n3605 N Damen Ave, Rear\n60618 Chicago, IL\n</code></pre>\n<h3>Stockwerke</h3>\n<p>Je nach Land werden die Stockwerke anders Nummeriert. Insbesondere in <a href=\"https://en.wikipedia.org/wiki/Storey\" target=\"_blank\">Nordamerika</a> werden die Stockwerke anders gezhlt als in Europa.</p>\n<p><img src=\"./img/SVG/storey.svg\" alt=\"\"></p>\n<h3>Strassenlogik &amp; Navigation</h3>\n<p>Gewisse Stdte weisen eine Logik der Strasennamen auf oder der Husernummerierung auf. Dadurch ist es in gewissen Stdten fr einheimische sehr einfach mglich mit Anzahl Strassen/Blocks zu navigierene. Auch ist in gewissen Stdten den einheimischen die Himmelsrichtung sehr bewusst. In Zrich wissen die meisten Leute zum Beispiel nicht in welche Richtung Sden ist. In London oder Chicago jedoch sehr wohl.</p>\n<style>\n.map-responsive{\n    overflow:hidden;\n    padding-bottom:56.25%;\n    position:relative;\n    height:0;\n}\n.map-responsive iframe{\n    left:0;\n    top:0;\n    height:100%;\n    width:100%;\n    position:absolute;\n}\n</style>\n<h3>Beispiel New York (Manhattan)</h3>\n<p>In Manhattan werden (teilweise) Strassen (WestOst) und Avenues (NordSd) unterschieden und diese nummeriert.</p>\n<div class=\"map-responsive\">\n    <iframe src=\"https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6043.751152669622!2d-73.95981316386695!3d40.76476126977315!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c2588f046ee661%3A0xa0b3281fcecc08c!2sManhattan%2C+New+York%2C+NY%2C+USA!5e0!3m2!1sen!2sch!4v1553908088407!5m2!1sen!2sch\" frameborder=\"0\" style=\"border:0\" allowfullscreen></iframe>\n</div>\n<h3>Beispiel Washington</h3>\n<p>In Washington werden Nummern (NordSd) und Buchstaben (WestOst) fr Strassen vergeben.</p>\n<div class=\"map-responsive\">\n  <iframe src=\"https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3105.088055169129!2d-77.02333333422055!3d38.89910164126649!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89b7c6de5af6e45b%3A0xc2524522d4885d2a!2sWashington%2C+DC%2C+USA!5e0!3m2!1sen!2sch!4v1553907503329!5m2!1sen!2sch\" frameborder=\"0\" style=\"border:0\" allowfullscreen></iframe>\n</div>\n<h2 id=\"telefonnummer\">Telefonnummer</h2>\n<div class=\"margin\">\n<p><a href=\"https://signalwerk.github.io/contact-parser/\" target=\"_blank\">Online-Helfer</a></p>\n</div>\n<p>Telefonnummern sollten gemss der Empfehlung von <a href=\"https://www.itu.int/rec/T-REC-E.123-200102-I/en\" target=\"_blank\">ITU-T Recommendation E.123</a> formatiert werden.</p>\n<h3>Beispiele</h3>\n<pre><code>Schweiz: +<span class=\"hljs-number\">41</span><span class=\"hljs-number\">78</span><span class=\"hljs-number\">700</span><span class=\"hljs-number\">10</span><span class=\"hljs-number\">10</span>\nDeutschland: +<span class=\"hljs-number\">49</span><span class=\"hljs-number\">341</span><span class=\"hljs-number\">2000000</span>\n</code></pre>\n<p> = Geschtzte schmales Leerzeichen fr typografisch korrekte Darstellung</p>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch<br>\n+41 78 744 37 38</p>\n<h4>Dokumentgeschichte</h4>\n<p>Mrz 2019: Erstellung</p>\n</div>\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<br />\n<div><!\nTextlnge\nsatzbreite\n### Typografie\n#### Wortlngen\n### Beispiele\n></div>\n"}

