	module.exports = {"title":"Navigations- und Ordnungskonzepte","body":"<h1 id=\"gestaltung-navigations-und-ordnungskonzepte\">Gestaltung  <br>Navigations- und Ordnungskonzepte</h1>\n<div class=\"TOC\">\n<p><strong>Content</strong>\n<div class=\"table-of-contents\"><ul><li><a href=\"#navigations-und-ordnungskonzepte\">Navigations- und Ordnungskonzepte</a></li><li><a href=\"#hierarchische-navigation\">Hierarchische Navigation</a></li><li><a href=\"#metanavigation\">Metanavigation</a></li><li><a href=\"#zeitliche-navigation\">Zeitliche Navigation</a></li><li><a href=\"#scroll-navigation-anker\">Scroll-Navigation (Anker)</a></li><li><a href=\"#storrytelling-durch-scroll-snow-fall\">Storrytelling durch scroll (Snow Fall)</a></li><li><a href=\"#navigation-auf-mobilen-geraten\">Navigation auf mobilen Gerten</a></li><li><a href=\"#weiterfuhrende-informationen\">Weiterfhrende Informationen</a></li></ul></div></p>\n</div>\n<div class='header'></div>\n<h2 id=\"navigations-und-ordnungskonzepte\">Navigations- und Ordnungskonzepte</h2>\n<p>Um den Inhalt einer Website mglichst einfach zugnglich zu machen, gibt es diverse Konzepte, diesen zu strukturieren und dem Benutzer eine einfache Navigation zu ermglichen. Je nach Inhalt, Umfang und Verwendungszweck knnen verschiedene Konzepte angewendet werden. Nicht alleinig der Inhalt, sondern auch das Anzeigegert kann eine entscheidende Rolle spielen, wie dies geschehen sollte. So wird bei einem Smartphone der Zugang durch einen kleinen Bildschirm ber eine Fingersteuerung gewhrleistet, whrend man bei einem Desktop-Computer mit der Maus auf einem grossem Bildschirm navigiert.</p>\n<h3>Strukturieren der Inhalte in Seiten</h3>\n<p>Die meisten Websites gliedern Inhalte ber Seiten. Meist wird auf einer Seite ein Thema, Artikel oder eine Einheite erlutert. Davon ausgehend werden dann Links und Bezge zu anderen Seiten geschaffen.</p>\n<br>\n<div class=\"margin wide\">\n<h4>Mehrere Inhalte auf mehreren Seiten</h4>\n</div>\n<p><img src=\"./img/Linking.svg\" alt=\"Verlinkung\"></p>\n<br>\n<br>\n<h3>Anwachsen der Themen ohne Struktur zu untersttzen</h3>\n<p>Wird ein Thema grsser, so knnen verschiedene Konzepte gewhlt werden, wie nun die Information weiter strukturiert wird. Meist ungnstig ist eine Aufteilung, die eine bestehende Struktur nur aufteilt, ohne aber den Benutzer weiter in der Struktur zu untersttzen.</p>\n<br>\n<div class=\"margin wide\">\n<h4>Paging oft ungnstig</h4>\n<p>Inhalte werden in kleinere Teile verteilt, jedoch wird die Struktur der Inhalte nicht Untersttzt.</p>\n</div>\n<p><img src=\"./img/paging.svg\" alt=\"Paging von Seiten\"></p>\n<div class='header'></div>\n<h3>Anwachsen der Themen mit erweiterter Struktur</h3>\n<p>Wachsen einzelne Themen an, knnen auch Kategorien gebildet werden und somit eine Gruppierung der Inhalte. Damit wird eine zustzliche Strukturtiefe eingezogen, jedoch mit dem Ziel die Struktur zu verstrken.</p>\n<br>\n<div class=\"margin wide\">\n<h4>Logische Einheiten</h4>\n<p>Die Hauptkategorie\numfasst mehrere Themen.</p>\n</div>\n<p><img src=\"./img/category.svg\" alt=\"Kategorien\"></p>\n<br>\n<h3>Strukturtiefe</h3>\n<p>Je mehr Inhalte in Kategorien zusammen gefasst werden mssen, desto eher entsteht das Bedrfnis, mehr Kategorien und Unterkategorien zu erstellen.</p>\n<div class=\"margin wide\">\n<h4>Unterkategoiren</h4>\n<p>Bei Webdesign spricht man von Levels, in die sich eine Struktur einteilen kann.</p>\n</div>\n<p><img src=\"./img/subcategory.svg\" alt=\"Unterkategorien\"></p>\n<div class='header'></div>\n<h2 id=\"hierarchische-navigation\">Hierarchische Navigation</h2>\n<p>Viele kleinere Websites lassen sich sehr einfach ber ein oder zwei Men-Tiefen (Men-Levels) steuern.</p>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/hyrarchy.svg\" alt=\"Hierarchie\"></p>\n</div>\n<div class='header'></div>\n<h3>Hierarchische Inhalte darstellen</h3>\n<p>Es gibt diverse Anstze um hierarchische Inhalte in einer Navigation ab zu bilden. Die Konzepte sind jedoch oftmals hnlich.</p>\n<br>\n<br>\n<div class=\"margin wide\">\n<h4>Abtrennen der Levels</h4>\n<p>Separate Navigations-Levels sind an verschiedenen Positionen angeordnet und optisch getrennt.</p>\n</div>\n<p><img src=\"./img/navigation_spread.svg\" alt=\"Navigation getrennt\"></p>\n<br>\n<div class=\"margin wide\">\n<h4>Verschachtelte Levels</h4>\n<p>Die Navigation besteht aus einer Einheit. Durch einrcken wird die jeweilige nchste Navigationstiefe angezeigt.</p>\n</div>\n<p><img src=\"./img/navigation_compact.svg\" alt=\"Navigation zusammen\"></p>\n<br>\n<div class=\"margin wide\">\n<h4>Breadcrumb-Navigation (Brotkrmelnavigation)</h4>\n<p>Kommen diverse Hierarchien innerhalb der Seite vor, wird hufig eine Breadcrumb-Navigation dem Benutzer zustzlich eingeblendet, so dass dieser immer Sieht, in welcher Kategoire und Unterkategorie er gerade ist und gegebenen Falles auch in der Hierarchie wieder zurck springen kann.</p>\n</div>\n<p><img src=\"./img/breadcrumb.svg\" alt=\"Breadcrumb-Navigation\"></p>\n<div class='header'></div>\n<h2 id=\"metanavigation\">Metanavigation</h2>\n<p>Bei einer Metanavigation werden nicht die Hierarchien innerhalb der Seite bercksichtig, sondern es werden gewisse Teile des Inhalts bewusst gewhlt und zusammen als separate Navigation ausgegeben.\nOft wird eine Metanavigation zur hierarchischen Navigation ergnzt.</p>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<p><img src=\"./img/meta_navigation.svg\" alt=\"Metanavigation\"></p>\n</div>\n<br>\n<br>\n<div class=\"margin wide\">\n<h4>Optische Abtrennung von Hierarchie-Navigation</h4>\n<p>Um dem Benutzer die Metanavigation verstndlicher zu machen, wird diese oftmals optisch abgetrennt.</p>\n</div>\n<p><img src=\"./img/meta_navigation_screen.svg\" alt=\"Metanavigation auf dem Bildschirm\"></p>\n<div class='header'></div>\n<h2 id=\"zeitliche-navigation\">Zeitliche Navigation</h2>\n<p>Werden viele Inhalte ohne erkennbare Struktur dargestellt, so kann eine Zeitliche Navigation  sofern der Inhalt dies zulsst  angebracht sein. Meist ist bei einer derartigen Navigation der aktuellste Eintrag (hufig nicht in Seiten strukturiert) am einfachsten zugnglich als am Anfang und weitere Eintrge folgen durch eine Navigation.</p>\n<br>\n<br>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<p><img src=\"./img/timeline.svg\" alt=\"Timeline\"></p>\n</div>\n<div class=\"col_8of12_last\">\n<p><img src=\"./img/time_navigation.svg\" alt=\"Timline Screen\"></p>\n</div>\n</div>\n<div class=\"grid fullsize\">\n<div class=\"col_4of12\">\n<h4>Timeline</h4>\n<p>Durch Scrollen oder verlinkung gelangt man zu den lteren Inhalten.</p>\n</div>\n<div class=\"col_8of12_last\">\n<h4>Posts geordnet nach Entstehung</h4>\n<p>Hufig werden Blogs oder Posts in sozialen Netzwerken mit absteigendem Datum sortiert.</p>\n</div>\n</div>\n<br>\n<br>\n<br>\n<div class=\"grid fullsize space2\">\n<div class=\"col_6of12\">\n<div class=\"imageline\">\n<p><img src=\"./img/glaettli.png\" alt=\"Twitter\"></p>\n</div>\n<h4>Posts geordnet nach Entstehung</h4>\n<p>Quelle: <a href=\"https://twitter.com/bglaettli\" target=\"_blank\">Balthasar Glttli  Twitter</a></p>\n</div>\n<div class=\"col_6of12_last\">\n<div class=\"imageline\">\n<p><img src=\"./img/time.png\" alt=\"Time\"></p>\n</div>\n<h4>Inhalt ber zeitliche Positionierung zugnglich</h4>\n<p>Quelle: <a href=\"http://world.time.com/2013/12/05/\" target=\"_blank\">Nelson Mandela  TIME.com</a>\nnelson-mandelas-extraordinary-life-an-interactive-timeline/</p>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"scroll-navigation-anker\">Scroll-Navigation (Anker)</h2>\n<p>Bei Seiten mit berschaubarem Inhalt, kann es sein, dass mehrere Themen auf einer einzelnen Seite angeordnet werden. Die Navigation erfollt dann innerhalb der Seite nur durch scrollen. Um Themen direkt an zu springen, kann auch eine Text-Navigation zustzlich eingeblendet sein.</p>\n<br>\n<br>\n<div class=\"margin wide\">\n<h3>Inhalte fortlaufend</h3>\n<p>Bei einer solchen Navigation sind die Inhalte nicht durch Seiten, sondern ber die Position getrennt.\nDie Navigation erfolgt dennoch ber ein Men, doch es wird kein Seitenwechsel, sondern ein Scroll ausgelst.</p>\n</div>\n<p><img src=\"./img/scroll_navigation.svg\" alt=\"Scroll-Navigation\"></p>\n<br>\n<br>\n<h2 id=\"storrytelling-durch-scroll-snow-fall\">Storrytelling durch scroll (Snow Fall)</h2>\n<p>Im Bereich des Journalismus gibt es diverse Anstze durch die Interaktivon des scrollens eine journalistische Geschichte einfacher zugnglich zu machen. Solch aufbereitete Seiten entstehen oft ausserhalb des blichen redaktionellen Prozesses und werden technisch und inhaltlich auf diese Art der Navigation optimiert.</p>\n<div class=\"grid fullsize space2\">\n<div class=\"col_6of12\">\n<div class=\"imageline\">\n<p><img src=\"./img/iouriA.png\" alt=\"Iouri Storry\"></p>\n</div>\n<h4>Bildstrecken, interaktive Grafiken und Gallerien untersttzen den Text</h4>\n<p>Quelle: <a href=\"http://iouri-in-sotschi.nzz.ch/\" target=\"_blank\">Iouri Podladtchikov - NZZ.ch</a></p>\n</div>\n<div class=\"col_6of12_last\">\n<div class=\"imageline\">\n<p><img src=\"./img/iouriB.png\" alt=\"Iouri Storry\"></p>\n</div>\n</div>\n</div>\n<div class='header'></div>\n<h2 id=\"navigation-auf-mobilen-geraten\">Navigation auf mobilen Gerten</h2>\n<p>Um auf den kleinen Bildschirmen der mobilen Gerte nicht viel Platz durch die Navigation zu verlieren, wird das Men meist vereinfacht und erst bei Anwahl des Mens vollstndig zugnglich gemacht.</p>\n<br>\n<br>\n<div class=\"margin wide\">\n<h4>Hamburger-Men mit Slideout</h4>\n<p>Als hufiges Symbol fr die Navigation werden drei Striche verwendet. Damit das Icon auch von weniger netzaffinen Personen klar erkennt werden kann, empfiehlt sich dies zustzlich an zu schreiben.\nDas Men bewegt sich von der Seite in den Bildschirm.</p>\n</div>\n<div class=\"grid w80p\">\n<p><img src=\"./img/mobile_slidein.svg\" alt=\"Mobile Navigation  slide-in\"></p>\n</div>\n<br>\n<br>\n<div class=\"margin wide\">\n<h4>Slidedown-Men</h4>\n<p>Das Slidedown-Men mit Button ist fr viele  auch ungebte Benutzer  ein bekanntes User-Interface und sie knnen dies auch als Men-Navigation bedienen.</p>\n</div>\n<div class=\"grid w80p\">\n<p><img src=\"./img/mobile_slidedown.svg\" alt=\"Mobile Navigation  slide-down\"></p>\n</div>\n<div class='header'></div>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch<br>\n+41 78 744 37 38</p>\n<h4>Dokumentgeschichte</h4>\n<p>Juni 2014: Erstellung<br>\nSeptember 2015: Anpassung<br>\nMrz 2017: Erweiterung</p>\n</div>\n<h2 id=\"weiterfuhrende-informationen\">Weiterfhrende Informationen</h2>\n<h3>Navigationen nach Zeit</h3>\n<ul>\n<li><a href=\"http://world.time.com/2013/12/05/nelson-mandelas-extraordinary-life-an-interactive-timeline/\" target=\"_blank\">Nelson Mandela  TIME.com</a></li>\n<li><a href=\"https://www.flickr.com/photos/holgi/\" target=\"_blank\">holgi | Flickr</a></li>\n<li><a href=\"https://twitter.com/bglaettli\" target=\"_blank\">Balthasar Glttli  Twitter</a></li>\n<li><a href=\"https://de-de.facebook.com/Zukkihund\" target=\"_blank\">Facebook</a></li>\n</ul>\n<h3>Scroll-Navigation</h3>\n<ul>\n<li><a href=\"http://nyphil.org/files/biennial/index.html\" target=\"_blank\">New York Philharmonic</a></li>\n<li><a href=\"http://www.outyear.co.uk/smint/demo/\" target=\"_blank\">Smint  jQuery plugin</a></li>\n</ul>\n<h3>Slideout</h3>\n<ul>\n<li><a href=\"http://mmenu.frebsite.nl/\" target=\"_blank\">sliding submenus  jQuery plugin</a></li>\n</ul>\n<h3>Storrytelling durch scroll</h3>\n<ul>\n<li><a href=\"http://www.nytimes.com/projects/2012/snow-fall/\" target=\"_blank\">Snow Fall  Multimedia Feature - NYTimes.com</a></li>\n<li><a href=\"http://monet.staedelmuseum.de\" target=\"_blank\">Monet im Stdel Museum Frankfurt</a></li>\n<li><a href=\"http://iouri-in-sotschi.nzz.ch/en/\" target=\"_blank\">Iouri Podladtchikov  NZZ.ch</a></li>\n<li><a href=\"http://www.tagesanzeiger.ch/extern/storytelling/platzspitz/\" target=\"_blank\">The Needle Trauma  tagesanzeiger.ch</a></li>\n<li><a href=\"http://www.bloomberg.com/graphics/2015-paul-ford-what-is-code/\" target=\"_blank\">Paul Ford: What Is Code?  Bloomberg</a></li>\n</ul>\n<h3>Aktuelle Web-Trends</h3>\n<ul>\n<li><a href=\"http://www.grimme-online-award.de\" target=\"_blank\">Grimme Online Award</a></li>\n</ul>\n"}

