	module.exports = {"title":"Parser  Grundlagen","body":"<h1 id=\"parser-grundlagen\">Parser  Grundlagen</h1>\n<h2 id=\"was-ist-ein-parser\">Was ist ein Parser?</h2>\n<div class=\"margin compact\">\n<h4>Quellen &amp; Weitere Beispiele</h4>\n<ul>\n<li><a href=\"https://de.wikipedia.org/wiki/Parser\" target=\"_blank\">Wikipedia  Parser</a></li>\n</ul>\n</div>\n<p>Ein Parser ist ein Stck Software, welches die Daten eines Input-Formats so interpretiert, dass nach dem Interpretieren der Computer damit arbeiten kann. Also aus einem Transportformat wird eine Native Reprsentanz erzeugt. Parser kommen in der Informatik in vielen Bereichen vor. Meist werden bei Schnittstellen zu einer Software Parser bentigt.</p>\n<h3>Beispiel HTML</h3>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>Hallo <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">em</span>&gt;</span>Welt!<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">em</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n</code></pre>\n<br />\nIm Browser wird dieses Stck HTML eher in einer Baumstruktur bentigt. Somit ist im Speicher/DOM des Browsers eher so eine Reprsentanz ntig.\n<br />\n<br />\n<pre><code class=\"language-yaml\"><span class=\"hljs-attr\">dom:</span>\n<span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">element</span>\n  <span class=\"hljs-attr\">tag:</span> <span class=\"hljs-string\">p</span>\n  <span class=\"hljs-attr\">children:</span>\n  <span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">text</span>\n    <span class=\"hljs-attr\">value:</span> <span class=\"hljs-string\">Hallo</span>\n<span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">element</span>\n  <span class=\"hljs-attr\">tag:</span> <span class=\"hljs-string\">em</span>\n  <span class=\"hljs-attr\">children:</span>\n  <span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">text</span>\n    <span class=\"hljs-attr\">value:</span> <span class=\"hljs-string\">Welt!</span>\n\n</code></pre>\n<h3>Weitere Beispiele</h3>\n<ul>\n<li>Damit der PDF-Viewer ein PDF darstellen kann, wird der Inhalt der Datei geparst</li>\n<li>Eine JavaScript-Datei wird interpregiert (geparst), so dass sie spter ausgefhrt werden kann.</li>\n</ul>\n<h2 id=\"bestandteile-eines-parsers\">Bestandteile eines Parsers</h2>\n<div class=\"margin compact\">\n<h4>Bestandteile Parser</h4>\n<ul>\n<li>Lexer  lexikalischer Scanner</li>\n<li>Tokenizer  interpretieren von Einheiten</li>\n</ul>\n</div>\n<p>Ein Parser ist oftmals in mehrere Arbeitsschritte aufgeteilt. Der Lexer interpretiert lexikalisch die eintreffenden Daten. Der Tokenizer grenzt ab, welcher Wert zu welchem Element gehrt und am Schluss wird das gewonnene Model vom Interpreter verarbeitet.</p>\n<h3>Beispiel Lexer HTML</h3>\n<pre><code>&lt; = Starttag\n<span class=\"hljs-attr\">p</span> = Tagname\n&gt; = Endtag\n<span class=\"hljs-attr\">H</span> = Text\n<span class=\"hljs-attr\">a</span> = Text\n<span class=\"hljs-attr\">l</span> = Text\n<span class=\"hljs-attr\">l</span> = Text\n<span class=\"hljs-attr\">o</span> = <span class=\"hljs-attr\">Text</span>\n  = Weissraum\n.\n.\n.\n</code></pre>\n<h3>Beispiel Tokenizer HTML</h3>\n<pre><code class=\"language-yaml\"><span class=\"hljs-attr\">dom:</span>\n<span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">element</span>\n  <span class=\"hljs-attr\">tag:</span> <span class=\"hljs-string\">p</span>\n  <span class=\"hljs-attr\">children:</span>\n  <span class=\"hljs-bullet\">-</span> <span class=\"hljs-attr\">type:</span> <span class=\"hljs-string\">text</span>\n    <span class=\"hljs-attr\">value:</span> <span class=\"hljs-string\">Hallo</span>\n<span class=\"hljs-string\">...</span>\n</code></pre>\n<h2 id=\"html-parser\">HTML Parser</h2>\n<div class=\"margin compact\">\n<h3>HTML-Parser vs. Browser</h3>\n<p>Ein Parser fr HTML zu schreiben ist relativ simpel. Aber ein voll funktionsfhiger Browser, der einsatztauglich ist, ist unglaublich komplex. Es gibt Weltweit nur eine handvoll Projekte, die dies gemacht haben.</p>\n</div>\n<p>Die Parser, welche HTML interpretieren sind ber Jahre gewachsen uns haben einen Beeindruckenden Funktionsumfang. Nicht nur HTML muss interpretiert werden, sondern diverse andere Teile mssen fr den Renderer (Darstellung) verstndlich gemacht werden. Dazu zhlen  zum Beispiel:</p>\n<ul>\n<li>Bild-Parser (jpg, gif, )</li>\n<li>Font-Parser</li>\n<li>CSS-Parser</li>\n<li>JavaScript-Parser</li>\n<li></li>\n</ul>\n<h4>Robustheit</h4>\n<p>HTML-Parser sind so programmiert, dass gewisse Fehler im HTML vom Browser aufgefangen werden knnen.</p>\n<h4>Beispiel Robustheit</h4>\n<p>Anstatt</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>Hallo <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">em</span>&gt;</span>Welt!<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">em</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n</code></pre>\n<br />\n<p>knnte auch eine fehlerhafte Eingabe vom Browser korrigiert werden:\n<br /></p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>Hallo <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">em</span>&gt;</span>Welt!\n</code></pre>\n<h3>CSS Parser</h3>\n<p>Auch der CSS Parser ist sehr fehlertolerant geschrieben. Definitionen, die vom Parser nicht verstanden werden, werden einfach ignoriert und der Parser bricht nicht einfach ab. Dies ermglich neuere CSS-Definitionen zu verwenden, die von alten Browsern ignoriert werden.</p>\n<h4>Fallback</h4>\n<p>Durch diese Robustheit knnen mehrere Definitionen fr das selbe definiert werden. Zu beachten ist, dass immer der unterste Wert der interpretiert werden kann gewinnt.</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">p</span> {\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">rgb</span>(<span class=\"hljs-number\">170</span>, <span class=\"hljs-number\">21</span>, <span class=\"hljs-number\">21</span>);    <span class=\"hljs-comment\">/* CSS2 */</span>\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">hsl</span>(<span class=\"hljs-number\">0</span>, <span class=\"hljs-number\">78%</span>, <span class=\"hljs-number\">37.5%</span>);  <span class=\"hljs-comment\">/* CSS Color Module Level 3 */</span>\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">lab</span>(<span class=\"hljs-number\">37</span>, <span class=\"hljs-number\">57</span>, <span class=\"hljs-number\">43</span>);     <span class=\"hljs-comment\">/* CSS Color Module Level 4 */</span>\n}\n</code></pre>\n<br />\n<p>Wird auf einem Browser, der nur <a href=\"https://www.w3.org/TR/CSS2/colors.html#q14.0\" target=\"_blank\">CSS 2</a> kann so interpretiert:</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">p</span> {\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">rgb</span>(<span class=\"hljs-number\">170</span>, <span class=\"hljs-number\">21</span>, <span class=\"hljs-number\">21</span>);    <span class=\"hljs-comment\">/* CSS2 */</span>\n}\n</code></pre>\n<br />\n<p>Wird auf einem Browser, der <a href=\"https://www.w3.org/TR/css-color-3/#hsl-color\" target=\"_blank\">CSS Color Module Level 3</a> kann so interpretiert:</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">p</span> {\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">hsl</span>(<span class=\"hljs-number\">0</span>, <span class=\"hljs-number\">78%</span>, <span class=\"hljs-number\">37.5%</span>);  <span class=\"hljs-comment\">/* CSS Color Module Level 3 */</span>\n}\n</code></pre>\n<br />\n<p>Wird auf einem Browser, der <a href=\"https://www.w3.org/TR/css-color-4/#specifying-lab-lch\" target=\"_blank\">CSS Color Module Level 4</a> kann so interpretiert:</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">p</span> {\n  <span class=\"hljs-attribute\">color</span>: <span class=\"hljs-built_in\">lab</span>(<span class=\"hljs-number\">37</span>, <span class=\"hljs-number\">57</span>, <span class=\"hljs-number\">43</span>);     <span class=\"hljs-comment\">/* CSS Color Module Level 4 */</span>\n}\n</code></pre>\n<h4>Prefix</h4>\n<div class=\"margin compact\">\n<p><a href=\"https://autoprefixer.github.io/\" target=\"_blank\">Online Autoprefixer</a></p>\n</div>\n<p>Wenn Browser-Hersteller gewisse funktionen neu implementieren, machen sie dies zum Teil mit so genannten Prefix-Flags. Anstatt das effektive CSS welches im Standard (spter) steht, nehmen Sie eine Version die mit dem Vendor-Name beginnt. Also zum Beispiel <code>webkit</code>.\nIn einer produktiven Umgebung wird das CSS meist normal geschrieben und die Prefixe werden automatisiert dazu gefgt.</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">div</span> {\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-built_in\">-webkit-gradient</span>(linear, left top, left bottom, from(white), <span class=\"hljs-built_in\">to</span>(red));\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-built_in\">-webkit-linear-gradient</span>(top, white, red);\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-built_in\">-o-linear-gradient</span>(top, white, red);\n  <span class=\"hljs-attribute\">background</span>: <span class=\"hljs-built_in\">linear-gradient</span>(to bottom, white, red);\n}\n</code></pre>\n<h3>HTML Ladezeiten</h3>\n<ul>\n<li>CSS loading</li>\n<li>Responsive Images</li>\n</ul>\n<h3>Videos Laden</h3>\n<ul>\n<li>Segmentieren/Scrub</li>\n</ul>\n<div class=\"margin printonly\">\n<h4>Autor</h4>\n<p>Stefan Huber<br>\nsh@signalwerk.ch<br>\n+41 78 744 37 38</p>\n<h4>Dokumentgeschichte</h4>\n<p>Juni 2019: Erstellung</p>\n<br />\n<br />\n<br />\n<br />\n<br />\n</div>\n<h2 id=\"weiterfuhrende-informationen\">Weiterfhrende Informationen</h2>\n<h3>Geschwindigkeit einer Website</h3>\n<ul>\n<li><a href=\"https://www.crazyegg.com/blog/speed-up-your-website/?fbclid=IwAR071e0G3wuavGTF5A5vv9Z9Dnmh7ebyKrcBi_5foIo44cwtb2ugp57bl8s\" target=\"_blank\">The Daily Egg  Speed</a></li>\n<li><a href=\"https://www.webpagetest.org/\" target=\"_blank\">WebPagetest</a></li>\n</ul>\n<h3>Responsive Bilder</h3>\n<ul>\n<li><a href=\"https://responsivebreakpoints.com/\" target=\"_blank\">Responsive Image Breakpoints Generator</a></li>\n<li><a href=\"https://cloudfour.com/thinks/responsive-images-101-part-5-sizes/\" target=\"_blank\">Responsive Images 101</a></li>\n</ul>\n"}

